<template>
    <div>

        <div v-if="httpLoad.process" class="loading">
            <div class="alert alert-dismissible alert-info" role="alert">
                <button class="close" disabled><i class="fa fa-spinner fa-spin pull-right"></i></button>
                {{ httpLoad.message }}
            </div>
        </div>

        <div v-if="!httpLoad.process && !httpLoad.access" class="error">
            <div class="alert alert-dismissible alert-danger" role="alert">
                {{ httpLoad.message }}
            </div>
        </div>

        <div v-if="httpLoad.access">

            <div class="page-header">
                <h2>Manage User - New User</h2>
            </div>

            <div class="row">
                <side-menu></side-menu>
                <div class="col-md-9">
                    <div class="panel panel-default">

                        <div class="panel-heading">
                            <h3 class="panel-title">New User</h3>
                        </div><!-- end .panel-heading -->

                        <div class="panel-body">

                            <div v-if="httpRequest.process" class="loading">
                                <div class="alert alert-dismissible alert-info" role="alert">
                                    <button class="close" disabled><i class="fa fa-spinner fa-spin pull-right"></i></button>
                                    {{ httpRequest.message }}
                                </div>
                            </div>

                            <div v-if="process_trigger && process_status.error" class="error">
                                <div class="alert alert-dismissible alert-danger" role="alert">
                                    <button type="button" class="close" @click="hideMsg"><span>&times;</span></button>
                                    {{ httpRequest.message }}
                                </div>
                            </div>

                            <div v-if="process_trigger && !process_status.error" class="success">
                                <div class="alert alert-dismissible alert-success" role="alert">
                                    <button type="button" class="close" @click="hideMsg"><span>&times;</span></button>
                                    {{ httpRequest.message }}
                                </div>
                            </div>

                            <form class="form-horizontal" v-on:submit.prevent="">

                                <template v-if="authUserStore.permission.set_user_group">
                                    <div :class="{'form-group' : true, 'has-error' : !process_loader && inputErrors.user_group_id}">
                                        <label for="usergroup" class="col-sm-3 control-label">User Group</label>
                                        <div class="col-sm-9">
                                            <select class="form-control" :disabled="process_loader" id="usergroup" name="usergroup" v-model="new_user.user_group_id">
                                                <template v-for='option in user_groups'>
                                                    <option :value="option.id" :selected="option.name == new_user.user_group_id">{{ option.name }}</option>
                                                </template>
                                            </select>
                                            <small v-if="!process_loader" v-for="error in inputErrors.user_group_id" class="help-block">
                                                <strong>{{ error }}</strong>
                                            </small>
                                        </div>
                                    </div>
                                </template>
                                <template v-else>
                                    <div class="form-group">
                                        <label for="usergroup" class="col-sm-3 control-label">User Group</label>
                                        <div class="col-sm-9">
                                            <p class="form-control-static"><span :class="'label label-' + $auth.userGroup(5).class_type">{{ $auth.userGroup(5).name_type }}</span></p>
                                        </div>
                                    </div>
                                </template>


                                <!-- start for Username -->
                                <div :class="{'form-group' : true, 'has-error' : !process_loader && inputErrors.username}">
                                    <label for="username" class="col-sm-3 control-label">Username</label>
                                    <div class="col-sm-9">
                                        <input type="text" class="form-control" :disabled="process_loader" id="username" name="username" v-model.trim="new_user.username" placeholder="Username">
                                        <small v-if="!process_loader" v-for="error in inputErrors.username" class="help-block">
                                            <strong>{{ error }}</strong>
                                        </small>
                                    </div>
                                </div>
                                <!-- end for Username -->

                                <!-- start for Password -->
                                <div :class="{'form-group' : true, 'has-error' : !process_loader && inputErrors.password}">
                                    <label for="password" class="col-sm-3 control-label">Password</label>
                                    <div class="col-sm-9">
                                        <input type="password" class="form-control" :disabled="process_loader" id="password" name="password" v-model.trim="new_user.password" placeholder="Password">
                                        <small v-if="!process_loader" v-for="error in inputErrors.password" class="help-block">
                                            <strong>{{ error }}</strong>
                                        </small>
                                    </div>
                                </div>
                                <!-- end for Email -->

                                <!-- start for Confirm Password -->
                                <div :class="{'form-group' : true, 'has-error' : !process_loader && inputErrors.password_confirmation}">
                                    <label for="email" class="col-sm-3 control-label">Confirm Password</label>
                                    <div class="col-sm-9">
                                        <input type="password" class="form-control" :disabled="process_loader" id="password_confirmation" name="password_confirmation" v-model.trim="new_user.password_confirmation" placeholder="Password Confirmation">
                                        <small v-if="!process_loader" v-for="error in inputErrors.password_confirmation" class="help-block">
                                            <strong>{{ error }}</strong>
                                        </small>
                                    </div>
                                </div>
                                <!-- end for Confirm Password -->

                                <!-- start for Email -->
                                <div :class="{'form-group' : true, 'has-error' : !process_loader && inputErrors.email}">
                                    <label for="email" class="col-sm-3 control-label">Email</label>
                                    <div class="col-sm-9">
                                        <input type="text" class="form-control" :disabled="process_loader" id="email" name="email" v-model.trim="new_user.email" placeholder="Email">
                                        <small v-if="!process_loader" v-for="error in inputErrors.email" class="help-block">
                                            <strong>{{ error }}</strong>
                                        </small>
                                    </div>
                                </div>
                                <!-- end for Email -->

                                <!-- start for Fullname -->
                                <div :class="{'form-group' : true, 'has-error' : !process_loader && inputErrors.fullname}">
                                    <label for="fullname" class="col-sm-3 control-label">Fullname</label>
                                    <div class="col-sm-9">
                                        <input type="text" class="form-control" :disabled="process_loader" id="fullname" name="fullname" v-model.trim="new_user.fullname" placeholder="Fullname">
                                        <small v-if="!process_loader" v-for="error in inputErrors.fullname" class="help-block">
                                            <strong>{{ error }}</strong>
                                        </small>
                                    </div>
                                </div>
                                <!-- end for Fullname -->

                                <template v-if="authUserStore.permission.set_user_status">
                                    <div :class="{'form-group' : true, 'has-error' : !process_loader && inputErrors.status_id}">
                                        <label for="status" class="col-sm-3 control-label">Status</label>
                                        <div class="col-sm-9">
                                            <select class="form-control" id="status" :disabled="process_loader" name="status" v-model="new_user.status_id">
                                                <template v-for='option in user_status'>
                                                    <option :value="option.id" :selected="option.id == new_user.status_id">{{ option.name }}</option>
                                                </template>
                                            </select>
                                            <small v-if="!process_loader" v-for="error in inputErrors.status_id" class="help-block">
                                                <strong>{{ error }}</strong>
                                            </small>
                                        </div>
                                    </div>
                                </template>
                                <template v-else>
                                    <div class="form-group">
                                        <label for="usergroup" class="col-sm-3 control-label">User Group</label>
                                        <div class="col-sm-9">
                                            <p class="form-control-static"><span :class="'label label-' + $auth.userStatus(1).class_type">{{ $auth.userStatus(1).name_type }}</span></p>
                                        </div>
                                    </div>
                                </template>

                                <div class="form-group">
                                    <div class="col-sm-offset-3 col-sm-9">
                                        <button type="submit" class="btn btn-primary" :disabled="process_loader" data-toggle="modal" data-target="#createUser">Create</button>
                                    </div>
                                </div>

                            </form><!-- end .form-horizontal -->

                        </div><!-- end .panel-body -->
                    </div><!-- end .panel .panel-default -->
                </div><!-- end .col-md-6 -->
            </div><!-- end .row -->
        </div><!-- end v-if="!fetchUserDataStatus.loading && !fetchUserDataStatus.error" -->
        <top-progress ref="topProgress"></top-progress>

        <!-- Small modal -->
        <div class="modal fade" id="createUser" tabindex="-1" role="dialog" aria-labelledby="mySmallModalLabel">
            <div class="modal-dialog modal-md" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true">&times;</span></button>
                        <h4 class="modal-title" id="myModalLabel">Confirmation</h4>
                    </div>
                    <div class="modal-body">
                        <p>Create user?</p>
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-default" data-dismiss="modal">No</button>
                        <button type="button" class="btn btn-primary" data-dismiss="modal" v-on:click="createuser_trigger">Yes</button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import {mapState} from 'vuex'
import _ from 'lodash'
import topProgress from 'vue-top-progress'
import {apiDomain, getHeader} from './../../config.js'
import SideMenu from './SideMenu.vue'
export default {
  mounted () {
    this.$refs.topProgress.start()
  },
  created () {
    console.log('Create User created.')
    this.loadData()
  },

  components: {
    SideMenu,
    topProgress
  },

  data () {
    return {
      httpLoad: {
        process: true,
        httpCode: 0,
        access: false,
        message: null
      },
      httpRequest: {
        process: false,
        trigger: false,
        httpCode: 0,
        message: null
      },
      new_user: {
        user_group_id: 5,
        username: null,
        password: null,
        password_confirmation: null,
        email: null,
        fullname: null,
        status_id: 1,
        parent_id: 0
      },
      user_status: [
        {id: 0, name: 'Deactivated'},
        {id: 1, name: 'Active'},
        {id: 2, name: 'Suspended'}
      ],
      inputErrors: [],
      permission: {}
    }
  },

  computed: {
    process_loader () {
      return this.httpRequest.process
    },

    process_trigger () {
      return !this.process_loader && this.httpRequest.trigger
    },

    process_status () {
      return {
        error: !this.process_loader && this.httpRequest.httpCode !== 200,
        message: this.httpRequest.message
      }
    },

    user_groups () {
      if (this.$store.state.authUserStore.profile.user_group_id === 1) {
        return [{id: 2, name: 'Ultimate'}, {id: 3, name: 'Premium'}, {id: 4, name: 'Reseller'}, {id: 5, name: 'Client'}]
      } else if (this.$store.state.authUserStore.user_group_id === 2) {
        return [{id: 3, name: 'Premium'}, {id: 4, name: 'Reseller'}, {id: 5, name: 'Client'}]
      } else if (this.$store.state.authUserStore.user_group_id === 3) {
        return [{id: 4, name: 'Reseller'}, {id: 5, name: 'Client'}]
      } else {
        return [{id: 5, name: 'Client'}]
      }
    },
    ...mapState({
      authUserStore: state => state.authUserStore,
      httpRequestStore: state => state.httpRequestStore
    })
  },

  methods: {
    loadData () {
      this.httpLoad.message = 'Accessing Page...'
      this.$http.get(apiDomain + 'manage-user/create', {headers: getHeader(this.$auth.getUserToken())}).then(response => {
        console.log('Create User: ' + response)

        var debounce = _.debounce(() => {
          this.$refs.topProgress.done()

          this.$store.dispatch('setAccountProfile', response.data.profile)
          this.$store.dispatch('setAccountPermission', response.data.permission)

          this.httpLoad.httpCode = response.status
          this.httpLoad.access = true
          this.httpLoad.process = false
        }, 1000)
        debounce()
      }).catch(error => {
        console.log(error.response)
        var errorMessage = 'Server Error.'
        var errorHttpCode = 0
        if (error.response) {
          errorHttpCode = error.response.status
          if (error.response.status === 401) {
            this.$auth.destroyUser()
            this.$store.dispatch('clearAuthUser')
            this.$router.push({name: 'login'})
          }
          if (error.response.status === 403 && error.response.data.message) {
            errorMessage = error.response.data.message
            this.$store.dispatch('setAccountProfile', error.response.data.profile)
            this.$store.dispatch('setAccountPermission', error.response.data.permission)
          }
        }
        var debounce = _.debounce(() => {
          this.$refs.topProgress.fail()
          this.httpLoad.httpCode = errorHttpCode
          this.httpLoad.message = errorMessage
          this.httpLoad.process = false
        }, 1000)
        debounce()
      })
    },

    hideMsg () {
      this.httpRequest.trigger = false
      this.inputErrors = []
    },

    createuser_trigger () {
      if (!this.process_loader) {
        this.httpRequest.message = 'Creating user...'
        this.httpRequest.trigger = true
        this.httpRequest.process = true
        this.inputErrors = []
        var debounce = _.debounce(() => this.createUser(), 500)
        debounce()
      }
    },

    createUser () {
      this.$http.post(apiDomain + 'manage-user/create', this.new_user, {headers: getHeader(this.$auth.getUserToken())}).then(response => {
        console.log('Create User : ' + response)

        var debounce = _.debounce(() => {
          this.new_user.user_group_id = 5
          this.new_user.username = null
          this.new_user.password = null
          this.new_user.password_confirmation = null
          this.new_user.email = null
          this.new_user.fullname = null
          this.new_user.status_id = 0

          this.httpRequest.httpCode = response.status
          this.httpRequest.message = response.data.message
          this.httpRequest.process = false
        }, 1000)
        debounce()
      }).catch(error => {
        console.log(error.response)
        var errorMessage = 'Server Error.'
        var errorHttpCode = 0
        if (error.response) {
          errorHttpCode = error.response.status
          if (error.response.status === 401) {
            this.$auth.destroyUser()
            this.$store.dispatch('clearAuthUser')
            this.$router.push({name: 'login'})
          }
          if (error.response.status === 422) {
            errorMessage = 'Input Error.'
            this.inputErrors = error.response.data
          } else if (error.response.data.message) {
            errorMessage = error.response.data.message
          }
        }
        var debounce = _.debounce(() => {
          this.httpRequest.httpCode = errorHttpCode
          this.httpRequest.message = errorMessage
          this.httpRequest.process = false
        }, 1000)
        debounce()
      })
    }
  }
}
</script>



