<template>
    <div>

        <div v-if="httpLoad.process" class="loading">
            <div class="alert alert-dismissible alert-info" role="alert">
                <button class="close" disabled><i class="fa fa-spinner fa-spin pull-right"></i></button>
                {{ httpLoad.message }}
            </div>
        </div>

        <div v-if="!httpLoad.process && !httpLoad.access" class="error">
            <div class="alert alert-dismissible alert-danger" role="alert">
                {{ httpLoad.message }}
            </div>
        </div>

        <div v-if="httpLoad.access">

            <div class="page-header">
                <h2>Vpn Server - Server List</h2>
            </div>

            <div class="row">
                <side-menu></side-menu>
                <div class="col-md-9">

                    <div class="panel panel-default">
                        <div class="panel-heading">
                            <h3 class="panel-title">Server List</h3>
                        </div>
                        <div class="panel-body">

                            <div v-if="httpRequest.process" class="loading">
                                <div class="alert alert-dismissible alert-info" role="alert">
                                    <button class="close" disabled><i class="fa fa-spinner fa-spin pull-right"></i></button>
                                    {{ httpRequest.message }}
                                </div>
                            </div>

                            <div v-if="process_trigger && process_status.error" class="error">
                                <div class="alert alert-dismissible alert-danger" role="alert">
                                    <button type="button" class="close" @click="hideMsg"><span>&times;</span></button>
                                    {{ httpRequest.message }}
                                </div>
                            </div>

                            <div v-if="process_trigger && !process_status.error" class="success">
                                <div class="alert alert-dismissible alert-success" role="alert">
                                    <button type="button" class="close" @click="hideMsg"><span>&times;</span></button>
                                    {{ httpRequest.message }}
                                </div>
                            </div>

                            <div class="table-responsive">
                                <table class="table table-striped table-condensed table-hover">
                                    <thead>
                                    <tr>
                                        <th><input type="checkbox" v-model="selectAll"></th>
                                        <th>Server Name</th>
                                        <th>Server IP</th>
                                        <th>Server Domain</th>
                                        <th>Online Users</th>
                                        <th>Access</th>
                                        <th>Status</th>
                                    </tr>
                                    </thead>
                                    <tbody>
                                    <tr v-for="row in model">
                                        <td><input type="checkbox" v-model="selected" :value="row.id" number></td>
                                        <td><router-link :to="{ name: 'vpnserver-info', params: { id: row.id }}">{{ row.server_name }}</router-link></td>
                                        <td>{{ row.server_ip }}</td>
                                        <td>{{ row.server_domain }}</td>
                                        <td>{{ row.online_users.length }}</td>
                                        <td>
                                            <template v-if="row.free_user==0">
                                                Premium
                                            </template>
                                            <template v-else>
                                                Free
                                            </template>
                                        </td>
                                        <td>
                                            <template v-if="row.is_active==1">
                                                Up
                                            </template>
                                            <template v-else>
                                                Down
                                            </template>
                                        </td>
                                    </tr>
                                    <tr v-if="model.data && model.length === 0">
                                        <td colspan="5">No server added</td>
                                    </tr>
                                    </tbody>
                                </table>

                                <button class="btn btn-danger" data-toggle="modal" data-target="#deleteServer">Delete</button>
                                <button class="btn btn-danger pull-right" data-toggle="modal" data-target="#downServer">Down</button>
                                <button class="btn btn-success pull-right" style="margin-right: 5px;" data-toggle="modal" data-target="#upServer">Up</button>

                                <button class="btn btn-default pull-right" style="margin-right: 10px;" data-toggle="modal" data-target="#freeServer">Free</button>
                                <button class="btn btn-primary pull-right" style="margin-right: 5px;" data-toggle="modal" data-target="#premiumServer">Premium</button>

                            </div>

                        </div><!-- end .panel-body -->
                    </div><!-- end .panel .panel-default -->

                </div><!-- end .col-md-9 -->

            </div><!-- end .row -->

        </div><!-- v-if="!fetchUserDataStatus.loading && !fetchUserDataStatus.error && permission.update_account" -->
        <top-progress ref="topProgress"></top-progress>

        <!-- Small modal -->
        <div class="modal fade" id="deleteServer" tabindex="-1" role="dialog" aria-labelledby="mySmallModalLabel">
            <div class="modal-dialog modal-md" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true">&times;</span></button>
                        <h4 class="modal-title" id="myModalLabel1">Confirmation</h4>
                    </div>
                    <div class="modal-body">
                        <p>
                            Delete servers?
                        </p>
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-default" data-dismiss="modal">No</button>
                        <button type="button" class="btn btn-primary" data-dismiss="modal" v-on:click="deleteserver_trigger">Yes</button>
                    </div>
                </div>
            </div>
        </div>

        <!-- Small modal -->
        <div class="modal fade" id="upServer" tabindex="-1" role="dialog" aria-labelledby="mySmallModalLabel">
            <div class="modal-dialog modal-md" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true">&times;</span></button>
                        <h4 class="modal-title" id="myModalLabel2">Confirmation</h4>
                    </div>
                    <div class="modal-body">
                        <p>
                            Up servers?
                        </p>
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-default" data-dismiss="modal">No</button>
                        <button type="button" class="btn btn-primary" data-dismiss="modal" v-on:click="upserver_trigger">Yes</button>
                    </div>
                </div>
            </div>
        </div>

        <!-- Small modal -->
        <div class="modal fade" id="downServer" tabindex="-1" role="dialog" aria-labelledby="mySmallModalLabel">
            <div class="modal-dialog modal-md" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true">&times;</span></button>
                        <h4 class="modal-title" id="myModalLabel3">Confirmation</h4>
                    </div>
                    <div class="modal-body">
                        <p>
                            Down servers?
                        </p>
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-default" data-dismiss="modal">No</button>
                        <button type="button" class="btn btn-primary" data-dismiss="modal" v-on:click="downserver_trigger">Yes</button>
                    </div>
                </div>
            </div>
        </div>

        <!-- Small modal -->
        <div class="modal fade" id="freeServer" tabindex="-1" role="dialog" aria-labelledby="mySmallModalLabel">
            <div class="modal-dialog modal-md" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true">&times;</span></button>
                        <h4 class="modal-title" id="myModalLabel4">Confirmation</h4>
                    </div>
                    <div class="modal-body">
                        <p>
                            Set server to free?
                        </p>
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-default" data-dismiss="modal">No</button>
                        <button type="button" class="btn btn-primary" data-dismiss="modal" v-on:click="freeserver_trigger">Yes</button>
                    </div>
                </div>
            </div>
        </div>

        <!-- Small modal -->
        <div class="modal fade" id="premiumServer" tabindex="-1" role="dialog" aria-labelledby="mySmallModalLabel">
            <div class="modal-dialog modal-md" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true">&times;</span></button>
                        <h4 class="modal-title" id="myModalLabel5">Confirmation</h4>
                    </div>
                    <div class="modal-body">
                        <p>
                            Set server to premium?
                        </p>
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-default" data-dismiss="modal">No</button>
                        <button type="button" class="btn btn-primary" data-dismiss="modal" v-on:click="premiumserver_trigger">Yes</button>
                    </div>
                </div>
            </div>
        </div>

    </div>
</template>

<script>
import {mapState} from 'vuex'
import _ from 'lodash'
import topProgress from 'vue-top-progress'
import {apiDomain, getHeader} from './../../config.js'
import SideMenu from './SideMenu.vue'
export default {
  mounted () {
    this.$refs.topProgress.start()
  },
  created () {
    console.log('Vpn Status created.')
    this.loadData()
  },

  components: {
    SideMenu,
    topProgress
  },

  data () {
    return {
      httpLoad: {
        process: true,
        httpCode: 0,
        access: false,
        message: null
      },
      httpRequest: {
        process: false,
        httpCode: 0,
        trigger: null,
        message: null
      },
      model: {},
      columns: {},
      selected: []
    }
  },

  computed: {
    selectAll: {
      get: function () {
        return this.model ? this.selected.length === this.model.length : false
      },
      set: function (value) {
        var selected = []

        if (value) {
          this.model.forEach(function (user) {
            selected.push(user.id)
          })
        }

        this.selected = selected
      }
    },
    process_loader () {
      return this.httpRequest.process
    },

    process_trigger () {
      return !this.process_loader && this.httpRequest.trigger
    },

    process_status () {
      return {
        error: !this.process_loader && this.httpRequest.httpCode !== 200,
        message: this.httpRequest.message
      }
    },

    ...mapState({
      authUserStore: state => state.authUserStore,
      httpRequestStore: state => state.httpRequestStore
    })
  },

  methods: {
    loadData () {
      this.httpLoad.message = 'Accessing Page...'
      this.$http.get(apiDomain + 'vpn-server/list', {headers: getHeader(this.$auth.getUserToken())}).then(response => {
        console.log(response)

        var debounce = _.debounce(() => {
          this.$refs.topProgress.done()

          this.$store.dispatch('setAccountProfile', response.data.profile)
          this.$store.dispatch('setAccountPermission', response.data.permission)

          this.model = response.data.model
          this.columns = response.data.columns

          this.httpLoad.httpCode = response.status
          this.httpLoad.access = true
          this.httpLoad.process = false
        }, 1000)
        debounce()
      }).catch(error => {
        console.log(error.response)
        var errorMessage = 'Server Error.'
        var errorHttpCode = 0
        if (error.response) {
          errorHttpCode = error.response.status
          if (error.response.status === 401) {
            this.$auth.destroyUser()
            this.$store.dispatch('clearAuthUser')
            this.$router.push({name: 'login'})
          }
          if (error.response.status === 403 && error.response.data.message) {
            errorMessage = error.response.data.message
            this.$store.dispatch('setAccountProfile', error.response.data.profile)
            this.$store.dispatch('setAccountPermission', error.response.data.permission)
          }
        }
        var debounce = _.debounce(() => {
          this.$refs.topProgress.fail()
          this.httpLoad.httpCode = errorHttpCode
          this.httpLoad.message = errorMessage
          this.httpLoad.process = false
        }, 1000)
        debounce()
      })
    },

    hideMsg () {
      this.httpRequest.trigger = false
      this.inputErrors = []
    },

    deleteserver_trigger () {
      if (!this.process_loader) {
        this.httpRequest.message = 'Deleting server...'
        this.httpRequest.trigger = true
        this.httpRequest.process = true
        this.inputErrors = []
        var debounce = _.debounce(() => this.delete_server(), 500)
        debounce()
      }
    },

    delete_server () {
      var deleteId = {
        id: this.selected
      }
      this.$http.post(apiDomain + 'vpn-server/delete-server', deleteId, {headers: getHeader(this.$auth.getUserToken())}).then(response => {
        console.log(response)

        var debounce = _.debounce(() => {
          this.selected = []

          this.model = response.data.model

          this.httpRequest.httpCode = response.status
          this.httpRequest.message = response.data.message
          this.httpRequest.process = false
        }, 1000)
        debounce()
      }).catch(error => {
        console.log(error.response)
        var errorMessage = 'Server Error.'
        var errorHttpCode = 0
        if (error.response) {
          errorHttpCode = error.response.status
          if (error.response.status === 401) {
            this.$auth.destroyUser()
            this.$store.dispatch('clearAuthUser')
            this.$router.push({name: 'login'})
          }
          if (error.response.data.message) {
            errorMessage = error.response.data.message
          }
        }
        var debounce = _.debounce(() => {
          this.httpRequest.httpCode = errorHttpCode
          this.httpRequest.message = errorMessage
          this.httpRequest.process = false
        }, 1000)
        debounce()
      })
    },

    upserver_trigger () {
      if (!this.process_loader) {
        this.httpRequest.message = 'Up server...'
        this.httpRequest.trigger = true
        this.httpRequest.process = true
        this.inputErrors = []
        var debounce = _.debounce(() => this.up_server(), 500)
        debounce()
      }
    },

    up_server () {
      var deleteId = {
        id: this.selected
      }
      this.$http.post(apiDomain + 'vpn-server/server-up', deleteId, {headers: getHeader(this.$auth.getUserToken())}).then(response => {
        console.log(response)

        var debounce = _.debounce(() => {
          this.selected = []

          this.model = response.data.model

          this.httpRequest.httpCode = response.status
          this.httpRequest.message = response.data.message
          this.httpRequest.process = false
        }, 1000)
        debounce()
      }).catch(error => {
        console.log(error.response)
        var errorMessage = 'Server Error.'
        var errorHttpCode = 0
        if (error.response) {
          errorHttpCode = error.response.status
          if (error.response.status === 401) {
            this.$auth.destroyUser()
            this.$store.dispatch('clearAuthUser')
            this.$router.push({name: 'login'})
          }
          if (error.response.data.message) {
            errorMessage = error.response.data.message
          }
        }
        var debounce = _.debounce(() => {
          this.httpRequest.httpCode = errorHttpCode
          this.httpRequest.message = errorMessage
          this.httpRequest.process = false
        }, 1000)
        debounce()
      })
    },

    downserver_trigger () {
      if (!this.process_loader) {
        this.httpRequest.message = 'Down server...'
        this.httpRequest.trigger = true
        this.httpRequest.process = true
        this.inputErrors = []
        var debounce = _.debounce(() => this.down_server(), 500)
        debounce()
      }
    },

    down_server () {
      var deleteId = {
        id: this.selected
      }
      this.$http.post(apiDomain + 'vpn-server/server-down', deleteId, {headers: getHeader(this.$auth.getUserToken())}).then(response => {
        console.log(response)

        var debounce = _.debounce(() => {
          this.selected = []

          this.model = response.data.model

          this.httpRequest.httpCode = response.status
          this.httpRequest.message = response.data.message
          this.httpRequest.process = false
        }, 1000)
        debounce()
      }).catch(error => {
        console.log(error.response)
        var errorMessage = 'Server Error.'
        var errorHttpCode = 0
        if (error.response) {
          errorHttpCode = error.response.status
          if (error.response.status === 401) {
            this.$auth.destroyUser()
            this.$store.dispatch('clearAuthUser')
            this.$router.push({name: 'login'})
          }
          if (error.response.data.message) {
            errorMessage = error.response.data.message
          }
        }
        var debounce = _.debounce(() => {
          this.httpRequest.httpCode = errorHttpCode
          this.httpRequest.message = errorMessage
          this.httpRequest.process = false
        }, 1000)
        debounce()
      })
    },

    freeserver_trigger () {
      if (!this.process_loader) {
        this.httpRequest.message = 'Setting server to free...'
        this.httpRequest.trigger = true
        this.httpRequest.process = true
        this.inputErrors = []
        var debounce = _.debounce(() => this.free_server(), 500)
        debounce()
      }
    },

    free_server () {
      var deleteId = {
        id: this.selected
      }
      this.$http.post(apiDomain + 'vpn-server/server-free', deleteId, {headers: getHeader(this.$auth.getUserToken())}).then(response => {
        console.log(response)

        var debounce = _.debounce(() => {
          this.selected = []

          this.model = response.data.model

          this.httpRequest.httpCode = response.status
          this.httpRequest.message = response.data.message
          this.httpRequest.process = false
        }, 1000)
        debounce()
      }).catch(error => {
        console.log(error.response)
        var errorMessage = 'Server Error.'
        var errorHttpCode = 0
        if (error.response) {
          errorHttpCode = error.response.status
          if (error.response.status === 401) {
            this.$auth.destroyUser()
            this.$store.dispatch('clearAuthUser')
            this.$router.push({name: 'login'})
          }
          if (error.response.data.message) {
            errorMessage = error.response.data.message
          }
        }
        var debounce = _.debounce(() => {
          this.httpRequest.httpCode = errorHttpCode
          this.httpRequest.message = errorMessage
          this.httpRequest.process = false
        }, 1000)
        debounce()
      })
    },

    premiumserver_trigger () {
      if (!this.process_loader) {
        this.httpRequest.message = 'Setting server to premium...'
        this.httpRequest.trigger = true
        this.httpRequest.process = true
        this.inputErrors = []
        var debounce = _.debounce(() => this.premium_server(), 500)
        debounce()
      }
    },

    premium_server () {
      var deleteId = {
        id: this.selected
      }
      this.$http.post(apiDomain + 'vpn-server/server-premium', deleteId, {headers: getHeader(this.$auth.getUserToken())}).then(response => {
        console.log(response)

        var debounce = _.debounce(() => {
          this.selected = []

          this.model = response.data.model

          this.httpRequest.httpCode = response.status
          this.httpRequest.message = response.data.message
          this.httpRequest.process = false
        }, 1000)
        debounce()
      }).catch(error => {
        console.log(error.response)
        var errorMessage = 'Server Error.'
        var errorHttpCode = 0
        if (error.response) {
          errorHttpCode = error.response.status
          if (error.response.status === 401) {
            this.$auth.destroyUser()
            this.$store.dispatch('clearAuthUser')
            this.$router.push({name: 'login'})
          }
          if (error.response.data.message) {
            errorMessage = error.response.data.message
          }
        }
        var debounce = _.debounce(() => {
          this.httpRequest.httpCode = errorHttpCode
          this.httpRequest.message = errorMessage
          this.httpRequest.process = false
        }, 1000)
        debounce()
      })
    }
  }
}
</script>



