<template>
    <div>

        <div v-if="httpLoad.process" class="loading">
            <div class="alert alert-dismissible alert-info" role="alert">
                <button class="close" disabled><i class="fa fa-spinner fa-spin pull-right"></i></button>
                {{ httpLoad.message }}
            </div>
        </div>

        <div v-if="!httpLoad.process && !httpLoad.access" class="error">
            <div class="alert alert-dismissible alert-danger" role="alert">
                {{ httpLoad.message }}
            </div>
        </div>

        <div v-if="httpLoad.access">

            <div class="page-header">
                <h2>Account Security</h2>
            </div>

            <div class="row">

                <side-menu></side-menu>

                <div class="col-md-9">

                    <div class="panel panel-default">

                        <div class="panel-heading">
                            <h3 class="panel-title">Security</h3>
                        </div>

                        <div class="panel-body">

                            <div v-if="httpRequest.process" class="loading">
                                <div class="alert alert-dismissible alert-info" role="alert">
                                    <button class="close" disabled><i class="fa fa-spinner fa-spin pull-right"></i></button>
                                    {{ httpRequest.message }}
                                </div>
                            </div>

                            <div v-if="process_trigger && process_status.error" class="error">
                                <div class="alert alert-dismissible alert-danger" role="alert">
                                    <button type="button" class="close" @click="hideMsg"><span>&times;</span></button>
                                    {{ httpRequest.message }}
                                </div>
                            </div>

                            <div v-if="process_trigger && !process_status.error" class="success">
                                <div class="alert alert-dismissible alert-success" role="alert">
                                    <button type="button" class="close" @click="hideMsg"><span>&times;</span></button>
                                    {{ httpRequest.message }}
                                </div>
                            </div>

                            <form class="form-horizontal" v-on:submit.prevent="">

                                <div  :class="{'form-group' : true, 'has-error' : !process_loader && inputErrors.password}">
                                    <label for="password" class="col-sm-3 control-label">Password</label>
                                    <div class="col-sm-9">
                                        <input type="password" class="form-control" :disabled="process_loader || !authUserStore.permission.update_account" id="password" name="password" v-model.trim="account.password" placeholder="Password">
                                        <small v-show="!process_loader" v-for="error in inputErrors.password" class="help-block">
                                            <strong>{{ error }}</strong>
                                        </small>
                                    </div>
                                </div>

                                <div :class="{'form-group' : true, 'has-error' : !process_loader && inputErrors.new_password}">
                                    <label for="new_password" class="col-sm-3 control-label">New Password</label>
                                    <div class="col-sm-9">
                                        <input type="password" class="form-control" :disabled="process_loader || !authUserStore.permission.update_account" id="new_password" name="new_password" v-model.trim="account.new_password" placeholder="New Password">
                                        <small v-show="!process_loader" v-for="error in inputErrors.new_password" class="help-block">
                                            <strong>{{ error }}</strong>
                                        </small>
                                    </div>
                                </div>

                                <div :class="{'form-group' : true, 'has-error' : !process_loader && inputErrors.new_password_confirmation}">
                                    <label for="new_password_confirmation" class="col-sm-3 control-label">New Password Confirmation</label>
                                    <div class="col-sm-9">
                                        <input type="password" class="form-control" :disabled="process_loader || !authUserStore.permission.update_account" id="new_password_confirmation" name="new_password_confirmation" v-model.trim="account.new_password_confirmation" placeholder="New Password Confirmation">
                                        <small v-show="!process_loader" v-for="error in inputErrors.new_password_confirmation" class="help-block">
                                            <strong>{{ error }}</strong>
                                        </small>
                                    </div>
                                </div>

                                <div class="form-group">
                                    <div class="col-sm-offset-3 col-sm-9">
                                        <button type="submit" class="btn btn-primary" :disabled="process_loader || !authUserStore.permission.update_account" data-toggle="modal" data-target="#accountSecurity">Save</button>
                                    </div>
                                </div>

                            </form>

                        </div><!-- end .panel-body -->

                    </div><!-- end .panel .panel-default -->

                </div><!-- end .col-md-9 -->

            </div><!-- end .row -->

        </div><!-- end v-if="!this.fetchUserDataStatus.loading && !this.fetchUserDataStatus.error && permission.update_account" -->
        <top-progress ref="topProgress"></top-progress>

        <!-- Small modal -->
        <div class="modal fade" id="accountSecurity" tabindex="-1" role="dialog" aria-labelledby="mySmallModalLabel">
            <div class="modal-dialog modal-md" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true">&times;</span></button>
                        <h4 class="modal-title" id="myModalLabel">Confirmation</h4>
                    </div>
                    <div class="modal-body">
                        <p>
                            Update security?
                        </p>
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-default" data-dismiss="modal">No</button>
                        <button type="button" class="btn btn-primary" data-dismiss="modal" v-on:click="updatepassword_trigger">Yes</button>
                    </div>
                </div>
            </div>
        </div>

    </div>
</template>

<style>
</style>

<script>
import {mapState} from 'vuex'
import _ from 'lodash'
import topProgress from 'vue-top-progress'
import {apiDomain, getHeader} from './../../config.js'
import SideMenu from './SideMenu.vue'
export default {
  mounted () {
    this.$refs.topProgress.start()
  },
  created () {
    console.log('Account Password created.')
    this.loadData()
  },

  components: {
    SideMenu,
    topProgress
  },

  computed: {
    process_loader () {
      return this.httpRequest.process
    },

    process_trigger () {
      return !this.process_loader && this.httpRequest.trigger
    },

    process_status () {
      return {
        error: !this.process_loader && this.httpRequest.httpCode !== 200,
        message: this.httpRequest.message
      }
    },
    ...mapState({
      authUserStore: state => state.authUserStore,
      httpRequestStore: state => state.httpRequestStore
    })
  },

  data () {
    return {
      httpLoad: {
        process: true,
        httpCode: 0,
        access: false,
        message: null
      },
      httpRequest: {
        process: false,
        httpCode: 0,
        trigger: null,
        message: null
      },
      inputErrors: [],
      account: {
        password: null,
        new_password: null,
        new_password_confirmation: null
      }
    }
  },

  methods: {
    hideMsg () {
      this.httpRequest.trigger = false
      this.inputErrors = []
    },

    updatepassword_trigger () {
      if (!this.process_loader) {
        this.httpRequest.message = 'Updating your password...'
        this.httpRequest.trigger = true
        this.httpRequest.process = true
        this.inputErrors = []
        var debounce = _.debounce(() => this.edit_password(), 500)
        debounce()
      }
    },

    edit_password () {
      this.$http.post(apiDomain + 'account/security', this.account, {headers: getHeader(this.$auth.getUserToken())}).then(response => {
        console.log(response)

        this.httpRequest.httpCode = response.status

        var debounce = _.debounce(() => {
          this.account = {}
          this.httpRequest.message = response.data.message
          this.httpRequest.process = false
        }, 1000)
        debounce()
      }).catch(error => {
        console.log(error.response)
        var errorMessage = 'Server Error.'
        var errorHttpCode = 0
        if (error.response) {
          errorHttpCode = error.response.status
          if (error.response.status === 401) {
            this.$auth.destroyUser()
            this.$store.dispatch('clearAuthUser')
            this.$router.push({name: 'login'})
          }
          if (error.response.status === 422) {
            errorMessage = 'Input Error.'
            this.inputErrors = error.response.data
          } else if (error.response.data.message) {
            errorMessage = error.response.data.message
          }
        }
        var debounce = _.debounce(() => {
          this.httpRequest.httpCode = errorHttpCode
          this.httpRequest.message = errorMessage
          this.httpRequest.process = false
        }, 1000)
        debounce()
      })
    },

    loadData () {
      this.httpLoad.message = 'Accessing Page...'
      this.$http.get(apiDomain + 'account/security', {headers: getHeader(this.$auth.getUserToken())}).then(response => {
        console.log(response)

        var debounce = _.debounce(() => {
          this.$refs.topProgress.done()

          this.$store.dispatch('setAccountProfile', response.data.profile)
          this.$store.dispatch('setAccountPermission', response.data.permission)

          this.httpLoad.httpCode = response.status
          this.httpLoad.access = true
          this.httpLoad.process = false
        }, 1000)
        debounce()
      }).catch(error => {
        console.log(error.response)
        var errorMessage = 'Server Error.'
        var errorHttpCode = 0
        if (error.response) {
          errorHttpCode = error.response.status
          if (error.response.status === 401) {
            this.$auth.destroyUser()
            this.$store.dispatch('clearAuthUser')
            this.$router.push({name: 'login'})
          }
          if (error.response.status === 403 && error.response.data.message) {
            errorMessage = error.response.data.message
            this.$store.dispatch('setAccountProfile', error.response.data.profile)
            this.$store.dispatch('setAccountPermission', error.response.data.permission)
          }
        }
        var debounce = _.debounce(() => {
          this.$refs.topProgress.fail()
          this.httpLoad.httpCode = errorHttpCode
          this.httpLoad.message = errorMessage
          this.httpLoad.process = false
        }, 1000)
        debounce()
      })
    }
  }
}
</script>



