<template>
    <div>

        <div v-if="httpLoad.process" class="loading">
            <div class="alert alert-dismissible alert-info" role="alert">
                <button class="close" disabled><i class="fa fa-spinner fa-spin pull-right"></i></button>
                {{ httpLoad.message }}
            </div>
        </div>

        <div v-if="!httpLoad.process && !httpLoad.access" class="error">
            <div class="alert alert-dismissible alert-danger" role="alert">
                {{ httpLoad.message }}
            </div>
        </div>

        <div v-if="httpLoad.access">

            <div class="page-header">
                <h2>Manage User - {{ username_display }}</h2>
            </div>

            <div class="row">
                <side-menu></side-menu>
                <div class="col-md-9">

                    <div class="panel panel-default">

                        <div class="panel-heading">
                            <h3 class="panel-title">User Profile <router-link v-if="authUserStore.permission.update_user_security" :to="{ name: 'user-security', params: { id: $route.params.id }}"><i class="glyphicon glyphicon-lock"></i></router-link> <router-link v-if="authUserStore.permission.is_admin && usergroup_static < 5" :to="{ name: 'user-permission', params: { id: $route.params.id }}"><i class="glyphicon glyphicon-scale"></i></router-link> <router-link v-if="authUserStore.permission.is_admin" :to="{ name: 'user-duration', params: { id: $route.params.id }}"><i class="glyphicon glyphicon-time"></i></router-link> <router-link v-if="authUserStore.permission.update_user_credits" :to="{ name: 'user-credits', params: { id: $route.params.id }}"><i class="fa fa-bars"></i></router-link> <router-link v-if="authUserStore.permission.apply_user_voucher" :to="{ name: 'user-voucher', params: { id: $route.params.id }}"><i class="fa fa-qrcode"></i></router-link></h3>
                        </div><!-- end .panel-heading -->

                        <div class="panel-body">

                            <div v-if="httpRequest.process" class="loading">
                                <div class="alert alert-dismissible alert-info" role="alert">
                                    <button class="close" disabled><i class="fa fa-spinner fa-spin pull-right"></i></button>
                                    {{ httpRequest.message }}
                                </div>
                            </div>

                            <div v-if="process_trigger && process_status.error" class="error">
                                <div class="alert alert-dismissible alert-danger" role="alert">
                                    <button type="button" class="close" @click="hideMsg"><span>&times;</span></button>
                                    {{ httpRequest.message }}
                                </div>
                            </div>

                            <div v-if="process_trigger && !process_status.error" class="success">
                                <div class="alert alert-dismissible alert-success" role="alert">
                                    <button type="button" class="close" @click="hideMsg"><span>&times;</span></button>
                                    {{ httpRequest.message }}
                                </div>
                            </div>

                            <form class="form-horizontal" v-on:submit.prevent="">

                                <template v-if="authUserStore.permission.update_user_usergroup">
                                    <div :class="{'form-group' : true, 'has-error' : !process_loader && inputErrors.user_group_id}">
                                        <label for="usergroup" class="col-sm-3 control-label">User Group</label>
                                        <div class="col-sm-9">
                                            <select class="form-control" :disabled="process_loader" id="usergroup" name="usergroup" v-model="user.user_group_id">
                                                <template v-for='option in user_groups'>
                                                    <option :value="option.id" :selected="option.id == user.user_group_id">{{ option.name }}</option>
                                                </template>
                                            </select>
                                            <small v-if="!process_loader" v-for="error in inputErrors.user_group_id" class="help-block">
                                                <strong>{{ error }}</strong>
                                            </small>
                                        </div>
                                    </div>
                                </template>
                                <template v-else>
                                    <div class="form-group">
                                        <label class="col-sm-3 control-label">User Group</label>
                                        <div class="col-sm-9">
                                            <p class="form-control-static"><span :class="'label label-' + $auth.userGroup(user.user_group_id).class_type">{{ $auth.userGroup(user.user_group_id).name_type }}</span></p>
                                        </div>
                                    </div>
                                </template>


                                <!-- start for Username -->
                                <template v-if="authUserStore.permission.is_admin">
                                    <div :class="{'form-group' : true, 'has-error' : !process_loader && inputErrors.username}">
                                        <label for="username" class="col-sm-3 control-label">Username</label>
                                        <div class="col-sm-9">
                                            <input type="text" class="form-control" :disabled="process_loader" id="username" name="username" v-model.trim="user.username" placeholder="Username">
                                            <small v-if="!process_loader" v-for="error in inputErrors.username" class="help-block">
                                                <strong>{{ error }}</strong>
                                            </small>
                                        </div>
                                    </div>
                                </template>
                                <template v-else>
                                    <div class="form-group">
                                        <label for="username" class="col-sm-3 control-label">Username</label>
                                        <div class="col-sm-9">
                                            <p class="form-control-static">{{ user.username }}</p>
                                        </div>
                                    </div>
                                </template>
                                <!-- end for Username -->

                                <!-- start for Email -->
                                <div :class="{'form-group' : true, 'has-error' : !process_loader && inputErrors.email}">
                                    <label for="email" class="col-sm-3 control-label">Email</label>
                                    <div class="col-sm-9">
                                        <input type="text" class="form-control" :disabled="process_loader" id="email" name="email" v-model.trim="user.email" placeholder="Email">
                                        <small v-if="!process_loader" v-for="error in inputErrors.email" class="help-block">
                                            <strong>{{ error }}</strong>
                                        </small>
                                    </div>
                                </div>
                                <!-- end for Email -->

                                <!-- start for Fullname -->
                                <div :class="{'form-group' : true, 'has-error' : !process_loader && inputErrors.fullname}">
                                    <label for="fullname" class="col-sm-3 control-label">Fullname</label>
                                    <div class="col-sm-9">
                                        <input type="text" class="form-control" :disabled="process_loader" id="fullname" name="fullname" v-model.trim="user.fullname" placeholder="Fullname">
                                        <small v-if="!process_loader" v-for="error in inputErrors.fullname" class="help-block">
                                            <strong>{{ error }}</strong>
                                        </small>
                                    </div>
                                </div>
                                <!-- end for Fullname -->

                                <template v-if="authUserStore.permission.update_user_status">
                                    <div :class="{'form-group' : true, 'has-error' : !process_loader && inputErrors.status}">
                                        <label for="status" class="col-sm-3 control-label">Status</label>
                                        <div class="col-sm-9">
                                            <select class="form-control" id="status" :disabled="process_loader" name="status" v-model="user.status_id">
                                                <template v-for='option in user_status'>
                                                    <option :value="option.id" :selected="option.id == user.status_id">{{ option.name }}</option>
                                                </template>
                                            </select>
                                            <small v-if="!process_loader" v-for="error in inputErrors.status" class="help-block">
                                                <strong>{{ error }}</strong>
                                            </small>
                                        </div>
                                    </div>
                                </template>
                                <template v-else>
                                    <div class="form-group">
                                        <label class="col-sm-3 control-label">Status :</label>
                                        <div class="col-sm-9">
                                            <p class="form-control-static"><span :class="'label label-' + $auth.userStatus(user.status_id).class_type">{{ $auth.userStatus(user.status_id).name_type }}</span></p>
                                        </div>
                                    </div>
                                </template>

                                <div class="form-group">
                                    <label class="col-sm-3 control-label">Expiration Date :</label>
                                    <div class="col-sm-9">
                                        <p class="form-control-static"><span :class="{'label ': true, 'label-default': user.expired_at === 'Expired', 'label-success': user.expired_at !== 'Expired'}">{{ user.expired_at }}</span></p>
                                    </div>
                                </div>

                                <div class="form-group">
                                    <label class="col-sm-3 control-label">Date Registered :</label>
                                    <div class="col-sm-9">
                                        <p class="form-control-static">{{ user.created_at }}</p>
                                    </div>
                                </div>

                                <div class="form-group">
                                    <label class="col-sm-3 control-label">Last Update :</label>
                                    <div class="col-sm-9">
                                        <p class="form-control-static">{{ user.updated_at }}</p>
                                    </div>
                                </div>

                                <div class="form-group">
                                    <div class="col-sm-offset-3 col-sm-9">
                                        <button type="submit" class="btn btn-primary" :disabled="process_loader" data-toggle="modal" data-target="#updateProfile">Save</button>
                                    </div>
                                </div>

                            </form><!-- end .form-horizontal -->

                        </div><!-- end .panel-body -->
                    </div><!-- end .panel .panel-default -->

                    <div class="panel panel-default">
                        <div class="panel-heading">
                            <h3 class="panel-title">VPN Status</h3>
                        </div>
                        <div class="panel-body">

                            <div v-if="httpVpn.process" class="loading">
                                <div class="alert alert-dismissible alert-info" role="alert">
                                    <button class="close" disabled><i class="fa fa-spinner fa-spin pull-right"></i></button>
                                    {{ httpVpn.message }}
                                </div>
                            </div>

                            <div v-if="vpn_trigger && vpn_status.error" class="error">
                                <div class="alert alert-dismissible alert-danger" role="alert">
                                    <button type="button" class="close" @click="hideMsgVpn"><span>&times;</span></button>
                                    {{ httpVpn.message }}
                                </div>
                            </div>

                            <div v-if="vpn_trigger && !vpn_status.error" class="success">
                                <div class="alert alert-dismissible alert-success" role="alert">
                                    <button type="button" class="close" @click="hideMsgVpn"><span>&times;</span></button>
                                    {{ httpVpn.message }}
                                </div>
                            </div>

                            <div class="table-responsive">
                                <table class="table table-striped table-condensed table-hover">
                                    <thead>
                                    <tr>
                                        <th>Server</th>
                                        <th>Download</th>
                                        <th>Upload</th>
                                        <th>Since</th>
                                        <th v-if="authUserStore.permission.is_admin" style="text-align: center;">Disconnect</th>
                                    </tr>
                                    </thead>
                                    <tbody>
                                    <tr v-for="row in user_vpn_session">
                                        <td>{{ row.vpnserver.server_name }}</td>
                                        <td>{{ row.byte_sent }}</td>
                                        <td>{{ row.byte_received }}</td>
                                        <td>{{ row.created_at }}</td>
                                        <td v-if="authUserStore.permission.is_admin" style="text-align: center;"><a href="" v-on:click.prevent="setServerId(row.vpn_server_id)" style="color: red;"><i class="fa fa-power-off" data-toggle="modal" data-target="#disconnectVpn"></i></a></td>
                                    </tr>
                                    <tr v-if="user_vpn_session.length==0">
                                        <td colspan="5">Not Connected</td>
                                    </tr>
                                    </tbody>
                                </table>

                            </div>

                        </div><!-- end .panel-body -->
                    </div><!-- end .panel .panel-default -->

                </div><!-- end .col-md-6 -->
            </div><!-- end .row -->
        </div><!-- end v-if="!fetchUserDataStatus.loading && !fetchUserDataStatus.error" -->
        <top-progress ref="topProgress"></top-progress>

        <!-- Small modal -->
        <div class="modal fade" id="updateProfile" tabindex="-1" role="dialog" aria-labelledby="mySmallModalLabel">
            <div class="modal-dialog modal-md" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true">&times;</span></button>
                        <h4 class="modal-title" id="userProfile">Confirmation</h4>
                    </div>
                    <div class="modal-body">
                        <p>Update user?</p>
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-default" data-dismiss="modal">No</button>
                        <button type="button" class="btn btn-primary" data-dismiss="modal" v-on:click="edituser_trigger">Yes</button>
                    </div>
                </div>
            </div>
        </div>

        <!-- Small modal -->
        <div class="modal fade" id="disconnectVpn" tabindex="-1" role="dialog" aria-labelledby="mySmallModalLabel">
            <div class="modal-dialog modal-md" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true">&times;</span></button>
                        <h4 class="modal-title" id="myModalLabel">Confirmation</h4>
                    </div>
                    <div class="modal-body">
                        <p>Disconnect vpn session?</p>
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-default" data-dismiss="modal">No</button>
                        <button type="button" class="btn btn-primary" data-dismiss="modal" v-on:click="disconnect_trigger(server.server_id)">Yes</button>
                    </div>
                </div>
            </div>
        </div>

    </div>
</template>

<script>
import {mapState} from 'vuex'
import _ from 'lodash'
import topProgress from 'vue-top-progress'
import {apiDomain, getHeader} from './../../config.js'
import SideMenu from './SideMenu.vue'
export default{
  mounted () {
    this.$refs.topProgress.start()
  },
  created () {
    console.log('User Profile created.')
    this.loadData()
  },

  components: {
    SideMenu,
    topProgress
  },

  data () {
    return {
      username_display: '',
      usergroup_static: 0,
      user: {},
      httpLoad: {
        process: true,
        httpCode: 0,
        access: false,
        message: null
      },
      httpRequest: {
        process: false,
        httpCode: 0,
        trigger: null,
        message: null
      },
      server: {
        server_id: 0
      },
      httpVpn: {
        process: false,
        httpCode: 0,
        trigger: null,
        message: null
      },
      user_vpn_session: {},
      user_status: [
        {id: 0, name: 'Deactivated'},
        {id: 1, name: 'Active'},
        {id: 2, name: 'Suspended'}
      ],
      inputErrors: []
    }
  },

  computed: {
    user_groups () {
      if (this.$store.state.authUserStore.profile.user_group_id === 1) {
        return [{id: 2, name: 'Ultimate'}, {id: 3, name: 'Premium'}, {id: 4, name: 'Reseller'}, {id: 5, name: 'Client'}]
      } else if (this.$store.state.authUserStore.profile.user_group_id === 2) {
        return [{id: 3, name: 'Premium'}, {id: 4, name: 'Reseller'}, {id: 5, name: 'Client'}]
      } else if (this.$store.state.authUserStore.profile.user_group_id === 3) {
        return [{id: 4, name: 'Reseller'}, {id: 5, name: 'Client'}]
      } else {
        return [{id: 5, name: 'Client'}]
      }
    },

    process_loader () {
      return this.httpRequest.process
    },

    vpn_loader () {
      return this.httpVpn.process
    },

    process_trigger () {
      return !this.process_loader && this.httpRequest.trigger
    },

    vpn_trigger () {
      return !this.vpn_loader && this.httpVpn.trigger
    },

    process_status () {
      return {
        error: !this.process_loader && this.httpRequest.httpCode !== 200,
        message: this.httpRequest.message
      }
    },

    vpn_status () {
      return {
        error: !this.vpn_loader && this.httpVpn.httpCode !== 200,
        message: this.httpVpn.message
      }
    },

    ...mapState({
      authUserStore: state => state.authUserStore,
      httpRequestStore: state => state.httpRequestStore
    })
  },

  methods: {
    loadData () {
      this.httpLoad.message = 'Accessing Page...'
      this.$http.get(apiDomain + 'manage-user/profile/' + this.$route.params.id, {headers: getHeader(this.$auth.getUserToken())}).then(response => {
        console.log(response)

        var debounce = _.debounce(() => {
          this.$refs.topProgress.done()

          this.$store.dispatch('setAccountProfile', response.data.profile)
          this.$store.dispatch('setAccountPermission', response.data.permission)

          this.user = response.data.user_profile
          this.username_display = this.user.username
          this.usergroup_static = this.user.user_group_id
          this.user_vpn_session = response.data.user_vpn_session

          this.httpLoad.httpCode = response.status
          this.httpLoad.access = true
          this.httpLoad.process = false
        }, 1000)
        debounce()
      }).catch(error => {
        console.log(error.response)
        var errorMessage = 'Server Error.'
        var errorHttpCode = 0
        if (error.response) {
          errorHttpCode = error.response.status
          if (error.response.status === 401) {
            this.$auth.destroyUser()
            this.$store.dispatch('clearAuthUser')
            this.$router.push({name: 'login'})
          }
          if (error.response.status === 403 && error.response.data.message) {
            errorMessage = error.response.data.message
            this.$store.dispatch('setAccountProfile', error.response.data.profile)
            this.$store.dispatch('setAccountPermission', error.response.data.permission)
          }
        }
        var debounce = _.debounce(() => {
          this.$refs.topProgress.fail()
          this.httpLoad.httpCode = errorHttpCode
          this.httpLoad.message = errorMessage
          this.httpLoad.process = false
        }, 1000)
        debounce()
      })
    },

    setServerId (id) {
      this.server.server_id = id
    },

    hideMsg () {
      this.httpRequest.trigger = false
      this.inputErrors = []
    },

    hideMsgVpn () {
      this.httpVpn.trigger = false
    },

    edituser_trigger () {
      if (!this.process_loader) {
        this.httpRequest.message = 'Updating User...'
        this.httpRequest.trigger = true
        this.httpRequest.process = true
        this.inputErrors = []
        var debounce = _.debounce(() => this.edit_user(), 500)
        debounce()
      }
    },

    edit_user () {
      this.$http.post(apiDomain + 'manage-user/profile/' + this.$route.params.id, this.user, {headers: getHeader(this.$auth.getUserToken())}).then(response => {
        console.log(response)
        this.httpRequest.httpCode = response.status

        var debounce = _.debounce(() => {
          this.user = response.data.user_profile
          this.username_display = this.user.username
          this.usergroup_static = this.user.user_group_id
          this.vpn_session = response.data.vpn_session
          this.httpRequest.message = response.data.message
          this.httpRequest.process = false
        }, 1000)
        debounce()
      }).catch(error => {
        console.log(error.response)
        var errorMessage = 'Server Error.'
        var errorHttpCode = 0
        if (error.response) {
          errorHttpCode = error.response.status
          if (error.response.status === 401) {
            this.$auth.destroyUser()
            this.$store.dispatch('clearAuthUser')
            this.$router.push({name: 'login'})
          }
          if (error.response.status === 422) {
            errorMessage = 'Input Error.'
            this.inputErrors = error.response.data
          } else if (error.response.data.message) {
            errorMessage = error.response.data.message
          }
        }
        var debounce = _.debounce(() => {
          this.httpRequest.httpCode = errorHttpCode
          this.httpRequest.message = errorMessage
          this.httpRequest.process = false
        }, 1000)
        debounce()
      })
    },

    disconnect_trigger () {
      if (!this.process_loader) {
        this.httpVpn.message = 'Disconnecting user\'s vpn session...'
        this.httpVpn.trigger = true
        this.httpVpn.process = true
        var debounce = _.debounce(() => this.disconnect_vpn(), 500)
        debounce()
      }
    },

    disconnect_vpn () {
      this.$http.post(apiDomain + 'manage-user/vpn-session/' + this.$route.params.id, this.server, {headers: getHeader(this.$auth.getUserToken())}).then(response => {
        console.log(response)
        this.httpVpn.httpCode = response.status

        var debounce = _.debounce(() => {
          this.httpVpn.message = response.data.message
          this.httpVpn.process = false
        }, 1000)
        debounce()
      }).catch(error => {
        console.log(error.response)
        var errorMessage = 'Server Error.'
        var errorHttpCode = 0
        if (error.response) {
          errorHttpCode = error.response.status
          if (error.response.status === 401) {
            this.$auth.destroyUser()
            this.$store.dispatch('clearAuthUser')
            this.$router.push({name: 'login'})
          }
          if (error.response.data.message) {
            errorMessage = error.response.data.message
          }
        }
        var debounce = _.debounce(() => {
          this.httpVpn.httpCode = errorHttpCode
          this.httpVpn.message = errorMessage
          this.httpVpn.process = false
        }, 1000)
        debounce()
      })
    }
  }
}
</script>



