<template>
    <div>

        <div v-if="httpLoad.process" class="loading">
            <div class="alert alert-dismissible alert-info" role="alert">
                <button class="close" disabled><i class="fa fa-spinner fa-spin pull-right"></i></button>
                {{ httpLoad.message }}
            </div>
        </div>

        <div v-if="!httpLoad.process && !httpLoad.access" class="error">
            <div class="alert alert-dismissible alert-danger" role="alert">
                {{ httpLoad.message }}
            </div>
        </div>

        <div v-if="httpLoad.access">

            <div class="page-header">
                <h2>Account Profile</h2>
            </div>

            <div class="row">
                <side-menu></side-menu>
                <div class="col-md-9">

                    <div class="row">
                        <div class="col-md-3 col-sm-6 col-xs-12">
                            <div class="info-box">
                                <span class="info-box-icon bg-green"><i class="glyphicon glyphicon-user"></i></span>

                                <div class="info-box-content">
                                    <span class="info-box-text">User Group</span>
                                    <span class="info-box-number"><span :class="'label label-' + $auth.userGroup(authUserStore.profile.user_group_id).class_type">{{ $auth.userGroup(authUserStore.profile.user_group_id).name_type }}</span></span>
                                </div>
                                <!-- /.info-box-content -->
                            </div>
                            <!-- /.info-box -->
                        </div>
                        <div class="col-md-3 col-sm-6 col-xs-12">
                            <div class="info-box">
                                <span class="info-box-icon bg-aqua"><i class="glyphicon glyphicon-certificate"></i></span>

                                <div class="info-box-content">
                                    <span class="info-box-text">Credits</span>
                                    <span class="info-box-number">
                                        <template v-if="authUserStore.profile.credits==='No Limit'">
                                            <span class="label label-success">{{ authUserStore.profile.credits }}</span>
                                        </template>
                                        <template v-else>
                                            <span :class="{'label ': true, 'label-primary': authUserStore.profile.credits > 0, 'label-danger': authUserStore.profile.credits == 0}">{{ authUserStore.profile.credits }}</span>
                                        </template>
                                    </span>
                                </div>
                                <!-- /.info-box-content -->
                            </div>
                            <!-- /.info-box -->
                        </div>
                        <div class="col-md-3 col-sm-6 col-xs-12">
                            <div class="info-box">
                                <span class="info-box-icon bg-red"><i class="glyphicon glyphicon-time"></i></span>

                                <div class="info-box-content">
                                    <span class="info-box-text">Expiration</span>
                                    <span class="info-box-number"><span :class="{'label ': true, 'label-default': authUserStore.profile.expired_at === 'Expired', 'label-success': authUserStore.profile.expired_at !== 'Expired'}">{{ authUserStore.profile.expired_at }}</span></span>
                                    <template v-if="!authUserStore.permission.is_admin">
                                        <span class="info-box-text">Data</span>
                                        <span class="info-box-number">
                                        <template v-if="authUserStore.profile.consumable_data=='0 Bytes'">
                                            <span class="label label-danger">{{ authUserStore.profile.consumable_data }} Free</span>
                                        </template>
                                        <template v-else>
                                            <span class="label label-success">{{ authUserStore.profile.consumable_data }} Free</span>
                                        </template>
                                        </span>
                                    </template>
                                </div>
                                <!-- /.info-box-content -->
                            </div>
                            <!-- /.info-box -->
                        </div>
                        <div class="col-md-3 col-sm-6 col-xs-12">
                            <div class="info-box">
                                <span class="info-box-icon bg-yellow"><i :class="{'glyphicon': true, 'glyphicon glyphicon-globe': authUserStore.vpn_session>0, 'glyphicon glyphicon-info-sign': authUserStore.vpn_session==0}"></i></span>

                                <div class="info-box-content">
                                    <span class="info-box-text">Status</span>
                                    <span class="info-box-number"><span :class="'label label-' + $auth.userStatus(authUserStore.profile.status_id).class_type">{{ $auth.userStatus(authUserStore.profile.status_id).name_type }}</span></span>
                                    <template v-if="!authUserStore.permission.is_admin">
                                        <span class="info-box-text">Package</span>
                                        <span v-if="!authUserStore.permission.is_admin" class="info-box-number">
                                            <span :class="{'label': true, 'label-primary': authUserStore.profile.vpn_session==1, 'label-success': authUserStore.profile.vpn_session==3, 'label-danger': authUserStore.profile.vpn_session==4}">
                                                <template v-if="authUserStore.profile.vpn_session==1">Bronze</template>
                                                <template v-if="authUserStore.profile.vpn_session==3">Silver</template>
                                                <template v-if="authUserStore.profile.vpn_session==4">Gold</template>
                                            </span>
                                        </span>
                                    </template>
                                </div>
                                <!-- /.info-box-content -->
                            </div>
                            <!-- /.info-box -->
                        </div>
                    </div>

                    <div class="panel panel-default">
                        <div class="panel-heading">
                            <h3 class="panel-title">Profile</h3>
                        </div>
                        <div class="panel-body">

                            <div v-if="httpRequest.process" class="loading">
                                <div class="alert alert-dismissible alert-info" role="alert">
                                    <button class="close" disabled><i class="fa fa-spinner fa-spin pull-right"></i></button>
                                    {{ httpRequest.message }}
                                </div>
                            </div>

                            <div v-if="process_trigger && process_status.error" class="error">
                                <div class="alert alert-dismissible alert-danger" role="alert">
                                    <button type="button" class="close" @click="hideMsg"><span>&times;</span></button>
                                    {{ httpRequest.message }}
                                </div>
                            </div>

                            <div v-if="process_trigger && !process_status.error" class="success">
                                <div class="alert alert-dismissible alert-success" role="alert">
                                    <button type="button" class="close" @click="hideMsg"><span>&times;</span></button>
                                    {{ httpRequest.message }}
                                </div>
                            </div>

                            <form class="form-horizontal" v-on:submit.prevent="">

                                <!-- start for Username -->
                                <template v-if="authUserStore.permission.is_admin">
                                    <div :class="{'form-group' : true, 'has-error' : !process_loader && inputErrors.username}">
                                        <label for="username" class="col-sm-3 control-label">Username</label>
                                        <div class="col-sm-9">
                                            <input type="text" class="form-control" :disabled="process_loader || !authUserStore.permission.update_account" id="username" name="username" v-model.trim="profile.username" placeholder="Username">
                                            <small v-if="!process_loader" v-for="error in inputErrors.username" class="help-block">
                                                <strong>{{ error }}</strong>
                                            </small>
                                        </div>
                                    </div>
                                </template>
                                <template v-else>
                                    <div class="form-group">
                                        <label for="username" class="col-sm-3 control-label">Username</label>
                                        <div class="col-sm-9">
                                            <p class="form-control-static">{{ profile.username }}</p>
                                        </div>
                                    </div>
                                </template>
                                <!-- end for Username -->

                                <div :class="{'form-group' : true, 'has-error' : !process_loader && inputErrors.email}">
                                    <label for="email" class="col-sm-3 control-label">Email</label>
                                    <div class="col-sm-9">
                                        <input type="email" class="form-control" :disabled="process_loader || !authUserStore.permission.update_account" id="email" name="email" v-model.trim="profile.email" placeholder="Email">
                                        <small v-show="!process_loader" v-for="error in inputErrors.email" class="help-block">
                                            <strong>{{ error }}</strong>
                                        </small>
                                    </div>
                                </div>

                                <div :class="{'form-group' : true, 'has-error' : !process_loader && inputErrors.fullname}">
                                    <label for="fullname" class="col-sm-3 control-label">Fullname</label>
                                    <div class="col-sm-9">
                                        <input type="text" class="form-control" :disabled="process_loader || !authUserStore.permission.update_account" id="fullname" name="fullname" v-model.trim="profile.fullname" placeholder="Fullname">
                                        <small v-show="!process_loader" v-for="error in inputErrors.fullname" class="help-block">
                                            <strong>{{ error }}</strong>
                                        </small>
                                    </div>
                                </div>

                                <div class="form-group">
                                    <label class="col-sm-3 control-label">Date Registered :</label>
                                    <div class="col-sm-9">
                                        <p class="form-control-static">{{ authUserStore.profile.created_at }}</p>
                                    </div>
                                </div>

                                <div class="form-group">
                                    <label class="col-sm-3 control-label">Last Update :</label>
                                    <div class="col-sm-9">
                                        <p class="form-control-static">{{ authUserStore.profile.updated_at }}</p>
                                    </div>
                                </div>

                                <div class="form-group">
                                    <div class="col-sm-offset-3 col-sm-9">
                                        <button type="submit" class="btn btn-primary" :disabled="process_loader || !authUserStore.permission.update_account" data-toggle="modal" data-target="#accountProfile">Save</button>
                                    </div>
                                </div>

                            </form>

                        </div><!-- end .panel-body -->
                    </div><!-- end .panel .panel-default -->
                </div><!-- end .col-md-9 -->
            </div><!-- end .row -->

        </div><!-- v-if="!fetchUserDataStatus.loading && !fetchUserDataStatus.error && permission.update_account" -->
        <top-progress ref="topProgress"></top-progress>

        <!-- Small modal -->
        <div class="modal fade" id="accountProfile" tabindex="-1" role="dialog" aria-labelledby="mySmallModalLabel">
            <div class="modal-dialog modal-md" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true">&times;</span></button>
                        <h4 class="modal-title" id="myModalLabel">Confirmation</h4>
                    </div>
                    <div class="modal-body">
                        <p>
                            Update profile?
                        </p>
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-default" data-dismiss="modal">No</button>
                        <button type="button" class="btn btn-primary" data-dismiss="modal" v-on:click="editaccount_trigger">Yes</button>
                    </div>
                </div>
            </div>
        </div>

    </div>
</template>

<script>
import {mapState} from 'vuex'
import _ from 'lodash'
import topProgress from 'vue-top-progress'
import {apiDomain, getHeader} from './../../config.js'
import SideMenu from './SideMenu.vue'
export default {
  mounted () {
    this.$refs.topProgress.start()
  },
  created () {
    console.log('Account Profile created.')
    this.loadData()
  },

  components: {
    SideMenu,
    topProgress
  },

  data () {
    return {
      httpLoad: {
        process: true,
        httpCode: 0,
        access: false,
        message: null
      },
      httpRequest: {
        process: false,
        httpCode: 0,
        trigger: null,
        message: null
      },
      profile: {},
      inputErrors: []
    }
  },

  computed: {
    process_loader () {
      return this.httpRequest.process
    },

    process_trigger () {
      return !this.process_loader && this.httpRequest.trigger
    },

    process_status () {
      return {
        error: !this.process_loader && this.httpRequest.httpCode !== 200,
        message: this.httpRequest.message
      }
    },

    ...mapState({
      authUserStore: state => state.authUserStore,
      httpRequestStore: state => state.httpRequestStore
    })
  },

  methods: {
    hideMsg () {
      this.httpRequest.trigger = false
      this.inputErrors = []
    },

    editaccount_trigger () {
      if (!this.process_loader) {
        this.httpRequest.message = 'Updating your account...'
        this.httpRequest.trigger = true
        this.httpRequest.process = true
        this.inputErrors = []
        var debounce = _.debounce(() => this.edit_account(), 500)
        debounce()
      }
    },

    edit_account () {
      this.$http.post(apiDomain + 'account/profile', this.profile, {headers: getHeader(this.$auth.getUserToken())}).then(response => {
        console.log(response)
        this.httpRequest.httpCode = response.status

        var debounce = _.debounce(() => {
          this.$store.dispatch('setAccountProfile', response.data.profile)
          this.httpRequest.message = response.data.message
          this.httpRequest.process = false
        }, 1000)
        debounce()
      }).catch(error => {
        console.log(error.response)
        var errorMessage = 'Server Error.'
        var errorHttpCode = 0
        if (error.response) {
          errorHttpCode = error.response.status
          if (error.response.status === 401) {
            this.$auth.destroyUser()
            this.$store.dispatch('clearAuthUser')
            this.$router.push({name: 'login'})
          }
          if (error.response.status === 422) {
            errorMessage = 'Input Error.'
            this.inputErrors = error.response.data
          } else if (error.response.data.message) {
            errorMessage = error.response.data.message
          }
        }
        var debounce = _.debounce(() => {
          this.httpRequest.httpCode = errorHttpCode
          this.httpRequest.message = errorMessage
          this.httpRequest.process = false
        }, 1000)
        debounce()
      })
    },

    loadData () {
      this.httpLoad.message = 'Accessing Page...'
      this.$http.get(apiDomain + 'account/profile', {headers: getHeader(this.$auth.getUserToken())}).then(response => {
        console.log(response)

        var debounce = _.debounce(() => {
          this.$refs.topProgress.done()

          this.$store.dispatch('setAccountProfile', response.data.profile)
          this.$store.dispatch('setAccountPermission', response.data.permission)
          this.$store.dispatch('setAccountVpn', response.data.vpn_session)

          this.profile.username = response.data.profile.username
          this.profile.email = response.data.profile.email
          this.profile.fullname = response.data.profile.fullname

          this.httpLoad.httpCode = response.status
          this.httpLoad.access = true
          this.httpLoad.process = false
        }, 500)
        debounce()
      }).catch(error => {
        console.log(error.response)
        var errorMessage = 'Server Error.'
        var errorHttpCode = 0
        if (error.response) {
          errorHttpCode = error.response.status
          if (error.response.status === 401) {
            this.$auth.destroyUser()
            this.$store.dispatch('clearAuthUser')
            this.$router.push({name: 'login'})
          }
          if (error.response.status === 403 && error.response.data.message) {
            errorMessage = error.response.data.message
            this.$store.dispatch('setAccountProfile', error.response.data.profile)
            this.$store.dispatch('setAccountPermission', error.response.data.permission)
          }
        }
        var debounce = _.debounce(() => {
          this.$refs.topProgress.fail()
          this.httpLoad.httpCode = errorHttpCode
          this.httpLoad.message = errorMessage
          this.httpLoad.process = false
        }, 1000)
        debounce()
      })
    }
  }
}
</script>



