<template>
    <div>

        <div v-if="httpLoad.process" class="loading">
            <div class="alert alert-dismissible alert-info" role="alert">
                <button class="close" disabled><i class="fa fa-spinner fa-spin pull-right"></i></button>
                {{ httpLoad.message }}
            </div>
        </div>

        <div v-if="!httpLoad.process && !httpLoad.access" class="error">
            <div class="alert alert-dismissible alert-danger" role="alert">
                {{ httpLoad.message }}
            </div>
        </div>

        <div v-if="httpLoad.access">

            <div class="page-header">
                <h2>Manage User - All Users</h2>
            </div>

            <div class="row">
                <side-menu></side-menu>
                <div class="col-md-9">

                    <div class="row">
                        <div class="col-md-6 col-sm-6 col-xs-12">
                            <div class="info-box">
                                <span class="info-box-icon bg-green"><i class="glyphicon glyphicon-user"></i></span>

                                <div class="info-box-content">
                                    <span class="info-box-text">Total Users</span>
                                    <span class="info-box-number">
                                        {{ total }}
                                    </span>
                                </div>
                                <!-- /.info-box-content -->
                            </div>
                            <!-- /.info-box -->
                        </div>
                        <div class="col-md-6 col-sm-6 col-xs-12">
                            <div class="info-box">
                                <span class="info-box-icon bg-aqua"><i class="glyphicon glyphicon-hourglass"></i></span>

                                <div class="info-box-content">
                                    <span class="info-box-text">New Users</span>
                                    <span class="info-box-number">
                                        {{ new_users }}
                                    </span>
                                </div>
                                <!-- /.info-box-content -->
                            </div>
                            <!-- /.info-box -->
                        </div>
                    </div>

                    <div class="panel panel-default">

                        <div class="panel-heading">
                            <h3 class="panel-title">View All Users</h3>
                        </div><!-- end .panel-heading -->

                        <div class="panel-body">

                            <form class="form-inline" v-on:submit.prevent="">
                                <div class="form-group">
                                    <label>Search By</label>
                                    <select class="form-control" v-model="query.search_column" :disabled="httpRequest.process" @change="trans_op(query.search_column)">
                                        <option value="id" :selected="query.search_column === 'id'">ID</option>
                                        <option value="username">Username</option>
                                        <option value="email">Email</option>
                                    </select>
                                </div>
                                <div class="form-group">
                                    <select class="form-control" v-model="query.search_operator" :disabled="httpRequest.process">
                                        <option v-for="(value, key) in operators" :value="key">{{value}}</option>
                                    </select>
                                </div>

                                <div class="form-group">
                                    <input type="text" class="form-control"
                                           placeholder="Search"
                                           v-model="query.search_input"
                                           @keyup.enter="fetchInitSearch()" :disabled="httpRequest.process">
                                </div>

                                <div class="form-group">
                                    <div class="btn-group" role="group" aria-label="...">
                                        <button type="button" :disabled="httpRequest.process" @click="changeStatusDisplay(-1)" :class="{'btn' : true, 'btn-primary' : query.status_id === -1, 'btn-default' : query.status_id !== -1}">All</button>
                                        <button type="button" :disabled="httpRequest.process" @click="changeStatusDisplay(0)" :class="{'btn' : true, 'btn-primary' : query.status_id === 0, 'btn-default' : query.status_id !== 0}">Deactivated</button>
                                        <button type="button" :disabled="httpRequest.process" @click="changeStatusDisplay(1)" :class="{'btn' : true, 'btn-primary' : query.status_id === 1, 'btn-default' : query.status_id !== 1}">Active</button>
                                        <button type="button" :disabled="httpRequest.process" @click="changeStatusDisplay(2)" :class="{'btn' : true, 'btn-primary' : query.status_id === 2, 'btn-default' : query.status_id !== 2}">Suspended</button>
                                    </div>
                                </div>

                            </form>

                            <div>&nbsp;</div>

                            <div v-if="httpRequest.process" class="loading">
                                <div class="alert alert-dismissible alert-info" role="alert">
                                    <button class="close" disabled><i class="fa fa-spinner fa-spin pull-right"></i></button>
                                    {{ httpRequest.message }}
                                </div>
                            </div>

                            <div v-if="process_trigger && process_status.error" class="error">
                                <div class="alert alert-dismissible alert-danger" role="alert">
                                    <button type="button" class="close" @click="hideMsg"><span>&times;</span></button>
                                    {{ httpRequest.message }}
                                </div>
                            </div>

                            <div v-if="process_trigger && !process_status.error" class="loading">
                                <div class="alert alert-dismissible alert-success" role="alert">
                                    <button type="button" class="close" @click="hideMsg"><span>&times;</span></button>
                                    {{ httpRequest.message }}
                                </div>
                            </div>

                            <div class="table-responsive" v-if="httpRequest.httpCode === 200">
                                <table class="table table-striped table-condensed table-hover">
                                    <thead>
                                    <tr>
                                        <th><input type="checkbox" v-model="selectAll"></th>
                                        <th v-for="column in columns" @click="toggleOrder(column)">
                                            <span :class="{'label label-default' : column === query.column}">{{ column_transformer(column) }}</span>
                                            <span v-if="column === query.column">
                                                <i v-if="query.direction === 'desc'" class="fa fa-sort-amount-desc"></i>
                                                <i v-else class="fa fa-sort-amount-asc"></i>
                                            </span>
                                        </th>
                                    </tr>
                                    </thead>
                                    <tbody>
                                    <tr v-for="row in model.data">
                                        <td><input type="checkbox" v-model="selected" :value="row.id" number></td>
                                        <td><router-link :to="{ name: 'user-profile', params: { id: row.id }}">{{ row.username }}</router-link></td>
                                        <td>{{ row.email }}</td>
                                        <td><span :class="'label label-' + $auth.userGroup(row.user_group_id).class_type">{{ $auth.userGroup(row.user_group_id).name_type }}</span></td>
                                        <td><span :class="'label label-' + $auth.userStatus(row.status_id).class_type">{{ $auth.userStatus(row.status_id).name_type }}</span></td>
                                        <td><span :class="{'label ': true, 'label-default': row.expired_at === 'Expired', 'label-success': row.expired_at !== 'Expired'}">{{ row.expired_at }}</span></td>
                                        <td>{{ row.created_at }}</td>
                                    </tr>
                                    <tr v-if="model.data && model.data.length === 0">
                                        <td colspan="6">No results</td>
                                    </tr>
                                    </tbody>
                                    <tfoot>
                                    <tr>
                                        <td colspan="6">

                                            <form class="form-inline" v-on:submit.prevent="">
                                                <div class="form-group">
                                                    <span>Displaying {{model.from}} - {{model.to}} of {{model.total}} rows Current Page: {{ model.current_page }} | </span>
                                                </div>

                                                <div class="form-group">
                                                    <label>Go to page</label>
                                                    <input type="text" v-model="query.page"
                                                           class="form-control"
                                                           @keyup.enter="goToPage()">
                                                </div>
                                                <div class="form-group">
                                                    <button class="btn btn-default" @click="prev()">&laquo;</button>
                                                </div>
                                                <div class="form-group">
                                                    <button class="btn btn-default" @click="next()">&raquo;</button>
                                                </div>
                                            </form>
                                        </td>
                                    </tr>
                                    </tfoot>
                                </table>

                                <button v-if="authUserStore.permission.delete_user" class="btn btn-danger" data-toggle="modal" data-target="#deleteUser">Delete</button>
                                <button v-if="authUserStore.permission.update_user_group" class="btn btn-default" data-toggle="modal" data-target="#deactivateUser">Deactivate</button>
                                <button v-if="authUserStore.permission.update_user_group" class="btn btn-primary" data-toggle="modal" data-target="#activateUser">Activate</button>
                                <button v-if="authUserStore.permission.update_user_group" class="btn btn-danger" data-toggle="modal" data-target="#suspendUser">Suspend</button>

                            </div>

                        </div><!-- end .panel-body -->
                    </div><!-- end .panel .panel-default -->
                </div><!-- end .col-md-6 -->
            </div><!-- end .row -->

        </div><!-- end v-if="page_display" -->
        <top-progress ref="topProgress"></top-progress>

        <!-- Small modal -->
        <div class="modal fade" id="deleteUser" tabindex="-1" role="dialog" aria-labelledby="mySmallModalLabel">
            <div class="modal-dialog modal-md" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true">&times;</span></button>
                        <h4 class="modal-title" id="myModalLabel">Confirmation</h4>
                    </div>
                    <div class="modal-body">
                        <p>
                            Delete users?
                        </p>
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-default" data-dismiss="modal">No</button>
                        <button type="button" class="btn btn-primary" data-dismiss="modal" v-on:click="delete_trigger">Yes</button>
                    </div>
                </div>
            </div>
        </div>

        <!-- Small modal -->
        <div class="modal fade" id="deactivateUser" tabindex="-1" role="dialog" aria-labelledby="mySmallModalLabel">
            <div class="modal-dialog modal-md" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true">&times;</span></button>
                        <h4 class="modal-title" id="myModalLabel1">Confirmation</h4>
                    </div>
                    <div class="modal-body">
                        <p>
                            Deactivate users?
                        </p>
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-default" data-dismiss="modal">No</button>
                        <button type="button" class="btn btn-primary" data-dismiss="modal" v-on:click="updatestatus_trigger(0)">Yes</button>
                    </div>
                </div>
            </div>
        </div>

        <!-- Small modal -->
        <div class="modal fade" id="activateUser" tabindex="-1" role="dialog" aria-labelledby="mySmallModalLabel">
            <div class="modal-dialog modal-md" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true">&times;</span></button>
                        <h4 class="modal-title" id="myModalLabel2">Confirmation</h4>
                    </div>
                    <div class="modal-body">
                        <p>
                            Activate users?
                        </p>
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-default" data-dismiss="modal">No</button>
                        <button type="button" class="btn btn-primary" data-dismiss="modal" v-on:click="updatestatus_trigger(1)">Yes</button>
                    </div>
                </div>
            </div>
        </div>

        <!-- Small modal -->
        <div class="modal fade" id="suspendUser" tabindex="-1" role="dialog" aria-labelledby="mySmallModalLabel">
            <div class="modal-dialog modal-md" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true">&times;</span></button>
                        <h4 class="modal-title" id="myModalLabel3">Confirmation</h4>
                    </div>
                    <div class="modal-body">
                        <p>
                            Suspend users?
                        </p>
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-default" data-dismiss="modal">No</button>
                        <button type="button" class="btn btn-primary" data-dismiss="modal" v-on:click="updatestatus_trigger(2)">Yes</button>
                    </div>
                </div>
            </div>
        </div>

    </div>
</template>

<script>
import {mapState} from 'vuex'
import _ from 'lodash'
import topProgress from 'vue-top-progress'
import {apiDomain, getHeader} from './../../config.js'
import SideMenu from './SideMenu.vue'
export default{
  mounted () {
    this.$refs.topProgress.start()
  },
  created () {
    console.log('All User created.')
    this.loadData()
  },

  components: {
    SideMenu,
    topProgress
  },

  data () {
    return {
      httpLoad: {
        process: true,
        httpCode: 0,
        access: false,
        message: null
      },
      httpRequest: {
        process: false,
        httpCode: 200,
        trigger: null,
        message: null
      },
      model: {},
      columns: {},
      total: 0,
      new_users: 0,
      query: {
        page: 1,
        column: 'created_at',
        direction: 'desc',
        per_page: 25,
        search_column: 'id',
        search_operator: 'equal',
        search_input: '',
        status_id: -1
      },
      operators: {
        equal: '=',
        not_equal: '<>',
        less_than: '<',
        greater_than: '>',
        less_than_or_equal_to: '<=',
        greater_than_or_equal_to: '>=',
        in: 'IN'
      },
      selected: []
    }
  },

  computed: {
    selectAll: {
      get: function () {
        return this.model.data ? this.selected.length === this.model.data.length : false
      },
      set: function (value) {
        var selected = []

        if (value) {
          this.model.data.forEach(function (user) {
            selected.push(user.id)
          })
        }

        this.selected = selected
      }
    },
    process_trigger () {
      return !this.httpRequest.process && this.httpRequest.trigger
    },

    process_status () {
      return {
        error: !this.httpRequest.process && this.httpRequest.httpCode !== 200,
        message: this.httpRequest.message
      }
    },

    sub_process () {
      return null
    },

    ...mapState({
      authUserStore: state => state.authUserStore,
      httpRequestStore: state => state.httpRequestStore
    })
  },

  methods: {
    loadData () {
      this.httpLoad.message = 'Accessing Page...'
      this.$http.get(apiDomain + `manage-user/all?column=${this.query.column}&direction=${this.query.direction}&page=${this.query.page}&per_page=${this.query.per_page}&search_column=${this.query.search_column}&search_operator=${this.query.search_operator}&search_input=${this.query.search_input}&status_id=${this.query.status_id}`, {headers: getHeader(this.$auth.getUserToken())}).then(response => {
        console.log(response)

        var debounce = _.debounce(() => {
          this.$refs.topProgress.done()

          this.$store.dispatch('setAccountProfile', response.data.profile)
          this.$store.dispatch('setAccountPermission', response.data.permission)

          this.model = response.data.model
          this.columns = response.data.columns
          this.total = response.data.total
          this.new_users = response.data.new_users

          this.httpLoad.httpCode = response.status
          this.httpLoad.access = true
          this.httpLoad.process = false
        }, 1000)
        debounce()
      }).catch(error => {
        console.log(error.response)
        var errorMessage = 'Server Error.'
        var errorHttpCode = 0
        if (error.response) {
          errorHttpCode = error.response.status
          if (error.response.status === 401) {
            this.$auth.destroyUser()
            this.$store.dispatch('clearAuthUser')
            this.$router.push({name: 'login'})
          }
          if (error.response.status === 403 && error.response.data.message) {
            errorMessage = error.response.data.message
            this.$store.dispatch('setAccountProfile', error.response.data.profile)
            this.$store.dispatch('setAccountPermission', error.response.data.permission)
          }
        }
        var debounce = _.debounce(() => {
          this.$refs.topProgress.fail()
          this.httpLoad.httpCode = errorHttpCode
          this.httpLoad.message = errorMessage
          this.httpLoad.process = false
        }, 1000)
        debounce()
      })
    },

    changeStatusDisplay (statusId) {
      this.query.status_id = statusId
      this.fetchInitSearch()
    },

    trans_op (col) {
      if (col === 'id') {
        this.operators = {}
        this.operators.equal = '='
        this.operators.not_equal = '<>'
        this.operators.less_than = '<'
        this.operators.greater_than = '>'
        this.operators.less_than_or_equal_to = '<='
        this.operators.greater_than_or_equal_to = '>='
        this.operators.in = 'IN'
      } else if (col === 'username' || col === 'email') {
        this.operators = {}
        this.operators.equal = '='
        this.operators.not_equal = '<>'
        this.operators.in = 'IN'
        this.operators.like = 'LIKE'
      }
    },

    column_transformer (col) {
      if (col === 'username') {
        return 'Username'
      }
      if (col === 'email') {
        return 'Email'
      }
      if (col === 'user_group_id') {
        return 'User Group'
      }
      if (col === 'status_id') {
        return 'Status'
      }
      if (col === 'expired_at') {
        return 'Duration'
      }
      if (col === 'created_at') {
        return 'Joined Date'
      }
    },

    next () {
      if (this.model.next_page_url) {
        this.query.page++
        this.fetchSearchData()
      }
    },
    prev () {
      if (this.model.prev_page_url) {
        this.query.page--
        this.fetchSearchData()
      }
    },
    toggleOrder (column) {
      if (!this.httpRequest.process) {
        this.httpRequest.message = 'Reordering...'
        if (column === this.query.column) {
          // only change direction
          if (this.query.direction === 'desc') {
            this.query.direction = 'asc'
          } else {
            this.query.direction = 'desc'
          }
        } else {
          this.query.column = column
          this.query.direction = 'asc'
        }
        this.fetchSearchData()
      }
    },
    fetchInitSearch () {
      this.query.page = 1
      this.fetchSearchData()
    },
    goToPage () {
      this.fetchSearchData()
    },

    fetchSearchData () {
      this.selected = []
      if (!this.httpRequest.process) {
        this.httpRequest.message = 'Fetching data...'
        this.httpRequest.process = true
        this.httpRequest.trigger = true
        this.$http.get(apiDomain + `manage-user/all?column=${this.query.column}&direction=${this.query.direction}&page=${this.query.page}&per_page=${this.query.per_page}&search_column=${this.query.search_column}&search_operator=${this.query.search_operator}&search_input=${this.query.search_input}&status_id=${this.query.status_id}`, {headers: getHeader(this.$auth.getUserToken())}).then(response => {
          console.log(response.data.model)
          var debounce = _.debounce(() => {
            this.model = response.data.model
            this.columns = response.data.columns

            this.httpRequest.httpCode = response.status
            this.httpRequest.process = false
            this.httpRequest.trigger = false
          }, 1000)
          debounce()
        }).catch(error => {
          console.log(error.response)
          var errorMessage = 'Server Error.'
          var errorHttpCode = 0
          if (error.response) {
            errorHttpCode = error.response.status
            if (error.response.status === 401) {
              this.$auth.destroyUser()
              this.$store.dispatch('clearAuthUser')
              this.$router.push({name: 'login'})
            }
            if (error.response.data.message) {
              errorMessage = error.response.data.message
            }
          }
          var debounce = _.debounce(() => {
            this.httpRequest.httpCode = errorHttpCode
            this.httpRequest.message = errorMessage
            this.httpRequest.process = false
          }, 1000)
          debounce()
        })
      }
    },
    hideMsg () {
      this.httpRequest.trigger = false
    },

    delete_trigger () {
      if (!this.httpRequest.process) {
        this.httpRequest.message = 'Deleting users...'
        this.httpRequest.trigger = true
        this.httpRequest.process = true
        this.inputErrors = []
        var debounce = _.debounce(() => this.userDelete(), 500)
        debounce()
      }
    },

    userDelete () {
      var deleteId = {
        id: this.selected,
        column: this.query.column,
        direction: this.query.direction,
        page: this.query.page,
        per_page: this.query.per_page,
        search_column: this.query.search_column,
        search_operator: this.query.search_operator,
        search_input: this.query.search_input,
        status_id: this.query.status_id
      }
      this.$http.post(apiDomain + 'manage-user/delete-all', deleteId, {headers: getHeader(this.$auth.getUserToken())}).then(response => {
        console.log(response)

        var debounce = _.debounce(() => {
          this.selected = []

          this.model = response.data.model
          this.columns = response.data.columns
          this.total = response.data.total
          this.new_users = response.data.new_users

          this.httpRequest.httpCode = response.status
          this.httpRequest.message = response.data.message
          this.httpRequest.process = false
        }, 1000)
        debounce()
      }).catch(error => {
        console.log(error.response)
        var errorMessage = 'Server Error.'
        var errorHttpCode = 0
        if (error.response) {
          errorHttpCode = error.response.status
          if (error.response.status === 401) {
            this.$auth.destroyUser()
            this.$store.dispatch('clearAuthUser')
            this.$router.push({name: 'login'})
          }
          if (error.response.data.message) {
            errorMessage = error.response.data.message
          }
        }
        var debounce = _.debounce(() => {
          this.httpRequest.httpCode = errorHttpCode
          this.httpRequest.message = errorMessage
          this.httpRequest.process = false
        }, 1000)
        debounce()
      })
    },

    updatestatus_trigger (statusId) {
      if (!this.httpRequest.process) {
        this.httpRequest.message = 'Updating user status...'
        this.httpRequest.trigger = true
        this.httpRequest.process = true
        this.inputErrors = []
        var debounce = _.debounce(() => this.updateUserStatus(statusId), 500)
        debounce()
      }
    },

    updateUserStatus (statusId) {
      var selectId = {
        id: this.selected,
        column: this.query.column,
        direction: this.query.direction,
        page: this.query.page,
        per_page: this.query.per_page,
        search_column: this.query.search_column,
        search_operator: this.query.search_operator,
        search_input: this.query.search_input,
        status_id: this.query.status_id,
        status_id_set: statusId
      }
      this.$http.post(apiDomain + 'manage-user/all-update-status', selectId, {headers: getHeader(this.$auth.getUserToken())}).then(response => {
        console.log(response)

        var debounce = _.debounce(() => {
          this.selected = []

          this.model = response.data.model
          this.columns = response.data.columns
          this.total = response.data.total
          this.new_users = response.data.new_users

          this.httpRequest.httpCode = response.status
          this.httpRequest.message = response.data.message
          this.httpRequest.process = false
        }, 1000)
        debounce()
      }).catch(error => {
        console.log(error.response)
        var errorMessage = 'Server Error.'
        var errorHttpCode = 0
        if (error.response) {
          errorHttpCode = error.response.status
          if (error.response.status === 401) {
            this.$auth.destroyUser()
            this.$store.dispatch('clearAuthUser')
            this.$router.push({name: 'login'})
          }
          if (error.response.data.message) {
            errorMessage = error.response.data.message
          }
        }
        var debounce = _.debounce(() => {
          this.httpRequest.httpCode = errorHttpCode
          this.httpRequest.message = errorMessage
          this.httpRequest.process = false
        }, 1000)
        debounce()
      })
    }
  }
}
</script>



