<template>
    <div>

        <div v-if="httpLoad.process" class="loading">
            <div class="alert alert-dismissible alert-info" role="alert">
                <button class="close" disabled><i class="fa fa-spinner fa-spin pull-right"></i></button>
                {{ httpLoad.message }}
            </div>
        </div>

        <div v-if="!httpLoad.process && !httpLoad.access" class="error">
            <div class="alert alert-dismissible alert-danger" role="alert">
                {{ httpLoad.message }}
            </div>
        </div>

        <div v-if="httpLoad.access">

            <div class="page-header">
                <h2>Vpn Server - Add Server</h2>
            </div>

            <div class="row">
                <side-menu></side-menu>
                <div class="col-md-9">

                    <div class="panel panel-default">

                        <div class="panel-heading">
                            <h3 class="panel-title"><router-link :to="{ name: 'vpnserver-list', params: { id: $route.params.id }}"><i class="glyphicon glyphicon-arrow-left"></i></router-link> Add Server</h3>
                        </div><!-- end .panel-heading -->

                        <div class="panel-body">

                            <div v-if="httpRequest.process" class="loading">
                                <div class="alert alert-dismissible alert-info" role="alert">
                                    <button class="close" disabled><i class="fa fa-spinner fa-spin pull-right"></i></button>
                                    {{ httpRequest.message }}
                                </div>
                            </div>

                            <div v-if="process_trigger && process_status.error" class="error">
                                <div class="alert alert-dismissible alert-danger" role="alert">
                                    <button type="button" class="close" @click="hideMsg"><span>&times;</span></button>
                                    {{ httpRequest.message }}
                                </div>
                            </div>

                            <div v-if="process_trigger && !process_status.error" class="success">
                                <div class="alert alert-dismissible alert-success" role="alert">
                                    <button type="button" class="close" @click="hideMsg"><span>&times;</span></button>
                                    {{ httpRequest.message }}
                                </div>
                            </div>

                            <form class="form-horizontal" v-on:submit.prevent="">

                                <div :class="{'form-group' : true, 'has-error' : !process_loader && inputErrors.server_name}">
                                    <label for="server_name" class="col-sm-3 control-label">Server Name</label>
                                    <div class="col-sm-9">
                                        <input type="text" class="form-control" :disabled="process_loader" id="server_name" name="server_name" v-model.trim="server_info.server_name" placeholder="Server Name">
                                        <small v-show="!process_loader" v-for="error in inputErrors.server_name" class="help-block">
                                            <strong>{{ error }}</strong>
                                        </small>
                                    </div>
                                </div>

                                <div :class="{'form-group' : true, 'has-error' : !process_loader && inputErrors.server_ip}">
                                    <label for="server_ip" class="col-sm-3 control-label">Server IP</label>
                                    <div class="col-sm-9">
                                        <input type="text" class="form-control" :disabled="process_loader" id="server_ip" name="server_ip" v-model.trim="server_info.server_ip" placeholder="Server IP">
                                        <small v-show="!process_loader" v-for="error in inputErrors.server_ip" class="help-block">
                                            <strong>{{ error }}</strong>
                                        </small>
                                    </div>
                                </div>

                                <div :class="{'form-group' : true, 'has-error' : !process_loader && inputErrors.server_domain}">
                                    <label for="server_domain" class="col-sm-3 control-label">Server Domain</label>
                                    <div class="col-sm-9">
                                        <input type="text" class="form-control" :disabled="process_loader" id="server_domain" name="server_domain" v-model.trim="server_info.server_domain" placeholder="Server Domain">
                                        <small v-show="!process_loader" v-for="error in inputErrors.server_domain" class="help-block">
                                            <strong>{{ error }}</strong>
                                        </small>
                                    </div>
                                </div>

                                <div :class="{'form-group' : true, 'has-error' : !process_loader && inputErrors.server_key}">
                                    <label for="server_key" class="col-sm-3 control-label">Server Key</label>
                                    <div class="col-sm-9">
                                        <div class="input-group">
                                            <input type="text" class="form-control" :disabled="process_loader" id="server_key" name="server_key" v-model.trim="server_info.server_key" placeholder="Server Key">
                                            <span class="input-group-btn">
                                                <button class="btn btn-danger" type="button" :disabled="process_loader" data-toggle="modal" data-target="#newKey">Get New!</button>
                                            </span>
                                        </div>
                                        <small v-show="!process_loader" v-for="error in inputErrors.server_key" class="help-block">
                                            <strong>{{ error }}</strong>
                                        </small>
                                    </div>
                                </div>

                                <div :class="{'form-group' : true, 'has-error' : !process_loader && inputErrors.vpn_secret}">
                                    <label for="vpn_secret" class="col-sm-3 control-label">Vpn Secret</label>
                                    <div class="col-sm-9">
                                        <input type="text" class="form-control" :disabled="process_loader" id="vpn_secret" name="vpn_secret" v-model.trim="server_info.vpn_secret" placeholder="Vpn Secret">
                                        <small v-show="!process_loader" v-for="error in inputErrors.vpn_secret" class="help-block">
                                            <strong>{{ error }}</strong>
                                        </small>
                                    </div>
                                </div>

                                <div :class="{'form-group' : true, 'has-error' : !process_loader && inputErrors.server_port}">
                                    <label for="server_port" class="col-sm-3 control-label">Server Port</label>
                                    <div class="col-sm-9">
                                        <input type="text" class="form-control" :disabled="process_loader" id="server_port" name="server_port" v-model.trim="server_info.server_port" placeholder="Server Port">
                                        <small v-show="!process_loader" v-for="error in inputErrors.server_port" class="help-block">
                                            <strong>{{ error }}</strong>
                                        </small>
                                    </div>
                                </div>

                                <div :class="{'form-group' : true, 'has-error' : !process_loader && inputErrors.server_access}">
                                    <label for="server_access" class="col-sm-3 control-label">Server Access</label>
                                    <div class="col-sm-9">
                                        <select class="form-control" id="server_access" :disabled="process_loader" name="server_access" v-model="server_info.server_access" number>
                                            <template v-for='option in server_access'>
                                                <option :value="option.id" :selected="option.id == server_info.server_access">{{ option.name }}</option>
                                            </template>
                                        </select>
                                        <small v-show="!process_loader" v-for="error in inputErrors.server_access" class="help-block">
                                            <strong>{{ error }}</strong>
                                        </small>
                                    </div>
                                </div>

                                <div :class="{'form-group' : true, 'has-error' : !process_loader && inputErrors.server_status}">
                                    <label for="server_status" class="col-sm-3 control-label">Server Status</label>
                                    <div class="col-sm-9">
                                        <select class="form-control" id="server_status" :disabled="process_loader" name="server_access" v-model="server_info.server_status" number>
                                            <template v-for='option in server_status'>
                                                <option :value="option.id" :selected="option.id == server_info.server_status">{{ option.name }}</option>
                                            </template>
                                        </select>
                                        <small v-show="!process_loader" v-for="error in inputErrors.server_status" class="help-block">
                                            <strong>{{ error }}</strong>
                                        </small>
                                    </div>
                                </div>

                                <div class="form-group">
                                    <div class="col-sm-offset-3 col-sm-9">
                                        <button type="submit" class="btn btn-primary" :disabled="process_loader" data-toggle="modal" data-target="#serverAdd">Save</button>
                                    </div>
                                </div>

                            </form><!-- end .form-horizontal -->

                        </div><!-- end .panel-body -->
                    </div><!-- end .panel .panel-default -->

                </div><!-- end .col-md-6 -->
            </div><!-- end .row -->
        </div><!-- end v-if="!fetchUserDataStatus.loading && !fetchUserDataStatus.error" -->
        <top-progress ref="topProgress"></top-progress>

        <!-- Small modal -->
        <div class="modal fade" id="serverAdd" tabindex="-1" role="dialog" aria-labelledby="mySmallModalLabel">
            <div class="modal-dialog modal-md" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true">&times;</span></button>
                        <h4 class="modal-title" id="myModalLabel">Confirmation</h4>
                    </div>
                    <div class="modal-body">
                        <p>Add server?</p>
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-default" data-dismiss="modal">No</button>
                        <button type="button" class="btn btn-primary" data-dismiss="modal" v-on:click="serveradd_trigger">Yes</button>
                    </div>
                </div>
            </div>
        </div>

        <!-- Small modal -->
        <div class="modal fade" id="newKey" tabindex="-1" role="dialog" aria-labelledby="mySmallModalLabel">
            <div class="modal-dialog modal-md" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true">&times;</span></button>
                        <h4 class="modal-title" id="myModalLabel1">Confirmation</h4>
                    </div>
                    <div class="modal-body">
                        <p>Generate new server key?</p>
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-default" data-dismiss="modal">No</button>
                        <button type="button" class="btn btn-primary" data-dismiss="modal" v-on:click="newkey_trigger">Yes</button>
                    </div>
                </div>
            </div>
        </div>

    </div>
</template>

<script>
import {mapState} from 'vuex'
import _ from 'lodash'
import topProgress from 'vue-top-progress'
import {apiDomain, getHeader} from './../../config.js'
import SideMenu from './SideMenu.vue'
export default{
  mounted () {
    this.$refs.topProgress.start()
  },
  created () {
    console.log('Server Info created1.')
    this.loadData()
  },

  components: {
    SideMenu,
    topProgress
  },

  data () {
    return {
      server_name: '',
      server_info: {},
      server_access: [
        {id: false, name: 'Premium'},
        {id: true, name: 'Free User'}
      ],
      server_status: [
        {id: false, name: 'Down'},
        {id: true, name: 'Up'}
      ],
      httpLoad: {
        process: true,
        httpCode: 0,
        access: false,
        message: null
      },
      httpRequest: {
        process: false,
        httpCode: 0,
        trigger: null,
        message: null
      },
      inputErrors: []
    }
  },

  computed: {
    process_loader () {
      return this.httpRequest.process
    },

    process_trigger () {
      return !this.process_loader && this.httpRequest.trigger
    },

    process_status () {
      return {
        error: !this.process_loader && this.httpRequest.httpCode !== 200,
        message: this.httpRequest.message
      }
    },

    ...mapState({
      authUserStore: state => state.authUserStore,
      httpRequestStore: state => state.httpRequestStore
    })
  },

  methods: {
    loadData () {
      this.httpLoad.message = 'Accessing Page...'
      this.$http.get(apiDomain + 'vpn-server/add', {headers: getHeader(this.$auth.getUserToken())}).then(response => {
        console.log(response)

        var debounce = _.debounce(() => {
          this.$refs.topProgress.done()

          this.$store.dispatch('setAccountProfile', response.data.profile)
          this.$store.dispatch('setAccountPermission', response.data.permission)

          this.httpLoad.httpCode = response.status
          this.httpLoad.access = true
          this.httpLoad.process = false
        }, 1000)
        debounce()
      }).catch(error => {
        console.log(error.response)
        var errorMessage = 'Server Error.'
        var errorHttpCode = 0
        if (error.response) {
          errorHttpCode = error.response.status
          if (error.response.status === 401) {
            this.$auth.destroyUser()
            this.$store.dispatch('clearAuthUser')
            this.$router.push({name: 'login'})
          }
          if (error.response.status === 403 && error.response.data.message) {
            errorMessage = error.response.data.message
            this.$store.dispatch('setAccountProfile', error.response.data.profile)
            this.$store.dispatch('setAccountPermission', error.response.data.permission)
          }
        }
        var debounce = _.debounce(() => {
          this.$refs.topProgress.fail()
          this.httpLoad.httpCode = errorHttpCode
          this.httpLoad.message = errorMessage
          this.httpLoad.process = false
        }, 1000)
        debounce()
      })
    },

    hideMsg () {
      this.httpRequest.trigger = false
      this.inputErrors = []
    },

    serveradd_trigger () {
      if (!this.process_loader) {
        this.httpRequest.message = 'Adding new server...'
        this.httpRequest.trigger = true
        this.httpRequest.process = true
        this.inputErrors = []
        var debounce = _.debounce(() => this.server_add(), 500)
        debounce()
      }
    },

    server_add () {
      this.$http.post(apiDomain + 'vpn-server/add', this.server_info, {headers: getHeader(this.$auth.getUserToken())}).then(response => {
        console.log(response)

        var debounce = _.debounce(() => {
          this.server_info = {}

          this.httpRequest.httpCode = response.status
          this.httpRequest.message = response.data.message
          this.httpRequest.process = false
        }, 1000)
        debounce()
      }).catch(error => {
        console.log(error.response)
        var errorMessage = 'Server Error.'
        var errorHttpCode = 0
        if (error.response) {
          errorHttpCode = error.response.status
          if (error.response.status === 401) {
            this.$auth.destroyUser()
            this.$store.dispatch('clearAuthUser')
            this.$router.push({name: 'login'})
          }
          if (error.response.status === 422) {
            errorMessage = 'Input Error.'
            this.inputErrors = error.response.data
          } else if (error.response.data.message) {
            errorMessage = error.response.data.message
          }
        }
        var debounce = _.debounce(() => {
          this.httpRequest.httpCode = errorHttpCode
          this.httpRequest.message = errorMessage
          this.httpRequest.process = false
        }, 1000)
        debounce()
      })
    },

    newkey_trigger () {
      if (!this.process_loader) {
        this.httpRequest.message = 'Generating new server key...'
        this.httpRequest.trigger = true
        this.httpRequest.process = true
        this.inputErrors = []
        var debounce = _.debounce(() => this.newkey(), 500)
        debounce()
      }
    },

    newkey () {
      this.$http.get(apiDomain + 'vpn-server/generatekey', {headers: getHeader(this.$auth.getUserToken())}).then(response => {
        console.log(response)

        var debounce = _.debounce(() => {
          this.server_info.server_key = response.data.newkey

          this.httpRequest.httpCode = response.status
          this.httpRequest.message = response.data.message
          this.httpRequest.process = false
        }, 1000)
        debounce()
      }).catch(error => {
        console.log(error.response)
        var errorMessage = 'Server Error.'
        var errorHttpCode = 0
        if (error.response) {
          errorHttpCode = error.response.status
          if (error.response.status === 401) {
            this.$auth.destroyUser()
            this.$store.dispatch('clearAuthUser')
            this.$router.push({name: 'login'})
          }
          if (error.response.data.message) {
            errorMessage = error.response.data.message
          }
        }
        var debounce = _.debounce(() => {
          this.httpRequest.httpCode = errorHttpCode
          this.httpRequest.message = errorMessage
          this.httpRequest.process = false
        }, 1000)
        debounce()
      })
    }
  }
}
</script>



